import { createContext, useCallback, useState } from "react";
import { useNavigate } from "react-router";

//const ENDPOINT = 'http://127.0.0.1/react_php-damax_boutique/backend'; // dev
const ENDPOINT = 'http://gucitex.com.mx/web-damaxboutique/backend'; // dist
const TOKEN_DEFAULT = '0eab84ea-917d-4817-9e33-c98caecf626d'; // only for getDataUserFromPass

export const DataContext = createContext();

export const DataProvider = ({ children }) => {
    // states 
    const [userSession, setUserSession] = useState(JSON.parse(window.sessionStorage.getItem('damax')));
    const [customerSession, setCustomerSession] = useState(JSON.parse(window.sessionStorage.getItem('customer')));
    const [articlesCart, setArticlesCart] = useState(
        window.localStorage.getItem('cart')
            ? JSON.parse(window.localStorage.getItem('cart'))
            : []
    );
    const [totalCart, setTotalCart] = useState(0)
    const [isReturnToCart, setIsReturnToCart] = useState(false)

    const navigate = useNavigate();

    const userCloseSession = useCallback(() => {
        window.sessionStorage.clear();
        setUserSession(null)
        navigate('/');
    }, [navigate])

    const customerCloseSession = useCallback(() => {
        window.sessionStorage.clear();
        setCustomerSession(null);
        navigate('/');
    }, [navigate])

    /*-------------- FETCH ----------------*/

    // !import into useCallback for no-reload infinite
    const fetchGet = useCallback(async (params, token = TOKEN_DEFAULT) => {
        const res = await fetch(ENDPOINT + params, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token
            }
        })
            .then($res => $res.text())
            .then(txt => {
                console.log(txt)
                console.log(JSON.parse(txt))
                return JSON.parse(txt)
            })
            .catch(error => {
                window.alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
                return null
            })

        // no authorized?
        if (res !== null) {
            //res['isAuth'] = false
            if (!res['isAuth']) {
                //console.log('EXIT SESSION')
                if (userSession) userCloseSession()
                else customerCloseSession()
            }
            delete res['isAuth']
        }

        return res
    }, [userCloseSession, customerCloseSession, userSession])

    // POST ONLY FOR INPUT USER-CUSTOMER
    async function fetchPostInput(params, body, token = TOKEN_DEFAULT) {
        const res = await fetch(ENDPOINT + params, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token
            },
            body: JSON.stringify(body)
        })
            .then($res => $res.text())
            .then(txt => {
                console.log(txt)
                return JSON.parse(txt)
            })
            .catch(error => {
                window.alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
                return null
            })

        return res
    }

    async function fetchPost(params, body, token = TOKEN_DEFAULT) {
        const res = await fetch(ENDPOINT + params, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token
            },
            body: JSON.stringify(body)
        })
            .then($res => $res.text())
            .then(txt => {
                console.log(txt)
                return JSON.parse(txt)
            })
            .catch(error => {
                window.alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
                return null
            })

        // no authorized?
        if (res !== null) {
            //res['isAuth'] = false
            if (!res['isAuth']) {
                //console.log('EXIT SESSION')
                if (userSession) userCloseSession()
                else customerCloseSession()
            }
            delete res['isAuth']
        }

        return res
    }

    async function fetchPut(params, body, token = TOKEN_DEFAULT) {
        const res = await fetch(ENDPOINT + params, {
            method: 'PUT',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token
            },
            body: JSON.stringify(body)
        })
            .then($res => $res.text())
            .then(txt => {
                console.log(txt)
                return JSON.parse(txt)
            })
            .catch(error => {
                window.alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
                return null
            })

        // no authorized?
        if (res !== null) {
            //res['isAuth'] = false
            if (!res['isAuth']) {
                //console.log('EXIT SESSION')
                if (userSession) userCloseSession()
                else customerCloseSession()
            }
            delete res['isAuth']
        }

        return res
    }

    async function fetchDelete(params, body, token = TOKEN_DEFAULT) {
        const res = await fetch(ENDPOINT + params, {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': token
            },
            body: JSON.stringify(body)
        })
            .then($res => $res.text())
            .then(txt => {
                console.log(txt)
                return JSON.parse(txt)
            })
            .catch(error => {
                window.alert('NO ES POSIBLE COMUNICARSE CON EL SERVIDOR')
                return null
            })

        // no authorized?
        if (res !== null) {
            //res['isAuth'] = false
            if (!res['isAuth']) {
                //console.log('EXIT SESSION')
                if (userSession) userCloseSession()
                else customerCloseSession()
            }
            delete res['isAuth']
        }

        return res
    }

    return (
        <DataContext.Provider value={{
            // ----- FETCH ------
            fetchGet,
            fetchPost,
            fetchPostInput,
            fetchPut,
            fetchDelete,
            // ----- USER-SESSION ------
            userSession,
            setUserSession,
            userCloseSession,
            //------ CART
            articlesCart,
            setArticlesCart,
            totalCart,
            setTotalCart,
            //------ CUSTOMER-SESSION
            customerSession,
            setCustomerSession,
            customerCloseSession,
            isReturnToCart,
            setIsReturnToCart
        }}>
            {children}
        </DataContext.Provider>
    );
}