// react
import { useState, useEffect, useContext, useCallback } from "react";
import { useNavigate } from 'react-router'
import { Link } from "react-router-dom";
// icons
import { AiOutlineClose, AiOutlineMenu, AiOutlineUser, AiOutlineSearch, AiOutlineShoppingCart, AiOutlineCloseCircle } from 'react-icons/ai';
import { CgUserList } from 'react-icons/cg';
// context
import { DataContext } from "../../context/DataContext";
// img
import logo from './logoDamax215x83.jpg';
// css
import './Header.css';

export default function Header() {
    // context
    const {
        fetchGet,
        userSession,
        userCloseSession,
        customerSession,
        customerCloseSession,
        articlesCart
    } = useContext(DataContext);

    // states
    const [isCategoriesMenu, setIsCategoriesMenu] = useState(false);
    const [isUserMenu, setIsUserMenu] = useState(false);
    const [isCustomerMenu, setIsCustomerMenu] = useState(false);
    const [categories, setCategories] = useState([]);
    const [isShowFinder, setIsShowFinder] = useState(false)
    const [txtSearch, setTxtSearch] = useState('')    

    // navigate
    const navigate = useNavigate()

    const categoriesLoad = useCallback(async () => {
        let res = await fetchGet('/index.php?fn=getAllCategories');
        //console.log('res:', res); // { categories[?] }
        if (res !== null) setCategories(res['categories'])
    }, [fetchGet])

    useEffect(() => {
        //console.log('>>> effect Header');

        setIsShowFinder(false) // hide finder
        setTxtSearch('') // clear text finder

        if (categories.length === 0) categoriesLoad() // only load the first time

        return () => { 'effect Header BYE' }

    }, [categoriesLoad, categories.length]);

    const timeOut = useCallback(() => {
        //console.log('timeOut()')
        if (txtSearch.length > 0) {
            navigate(`/productos/texto/${txtSearch}`)
        }
    }, [navigate, txtSearch])

    useEffect(() => {
        //console.log('txt real-time:', txtSearch) 

        // let to
        // if (isFirstLoad) {
        //     setIsFirstLoad(false)
        //     to = setTimeout(timeOut, 1000)
        // }
        const to = setTimeout(timeOut, 1000)
        
        return () => {
            clearTimeout(to)
        }
    }, [txtSearch, timeOut])

    const toggleCategoryMenu = () => {
        setIsCategoriesMenu(!isCategoriesMenu);
    }

    const toggleUserMenu = () => {
        setIsUserMenu(!isUserMenu);
    }

    const toggleCustomerMenu = () => {
        setIsCustomerMenu(!isCustomerMenu);
    }

    const closeSessionClick = () => {
        //console.log("closeSessionClick()");
        toggleUserMenu();

        // clear sessionStorage
        userCloseSession()
    }

    const closeSessionCustomerClick = () => {
        // close menu right of customer if is open
        if (isCustomerMenu) setIsCustomerMenu(false);

        // clear sessionStorage
        customerCloseSession()
    }

    function iconFinderClick() {
        setIsShowFinder(!isShowFinder)
    }

    function iconCloseFinderClick() {
        setIsShowFinder(false)
        setTxtSearch('')
    }

    async function inputChange(e) {
        setTxtSearch(e.target.value)                     
    }

    function headerClick(e) {
        //console.log(e.target)
        if (isShowFinder) setIsShowFinder(false)
    }

    return (
        <>
            <header className={userSession ? 'Header isUserSession' : customerSession ? 'Header isCustomerSession' : 'Header'} onClick={headerClick}>
 
                {/* --------------------- SIDEBAR LEFT MENU CATEGORIES ---------------------------------- */}
                <nav className={isCategoriesMenu ? 'Header-menu-categories active' : 'Header-menu-categories'}>
                    <AiOutlineClose className="Header-icon-close" onClick={toggleCategoryMenu} />
                    <ul className="Header-list">
                        <Link to="/" className="Header-link-item">
                            <li className="Header-item" onClick={toggleCategoryMenu}>INICIO</li>
                        </Link>
                        <hr />

                        {//------------------- LOAD CATEGORIES ---------------------//
                            categories.map(category =>
                                category['isAvailable'] &&
                                <div key={category.id}>
                                    <hr />
                                    {/* <Link to={`/productos/${category.categoryName}/true`} className='Header-link-item'> */}
                                    <Link to={`/productos/categoria/${category.categoryName}`} className='Header-link-item'>
                                        <li className="Header-item" onClick={toggleCategoryMenu}>{category.categoryName}</li>
                                    </Link>
                                </div>
                            )
                        }

                        {/*
                        <>
                            <Link to="/products/cadenas" className="Header-link-item">
                                <li className="Header-item" onClick={showSidebar}>CADENAS Y CHOKERS</li>
                            </Link>
                            <hr />
                            <Link to="/products/collares" className="Header-link-item">
                                <li className="Header-item" onClick={showSidebar}>COLLARES</li>
                            </Link>
                            <hr />
                            <Link to="/products/personalizados" className="Header-link-item">
                                <li className="Header-item" onClick={showSidebar}>PERSONALIZADOS</li>
                            </Link>
                        </>
                    */}
                    </ul>
                    <hr />

                    {  //---------------- only if not user in session -----------------//
                        !userSession && !customerSession &&
                        <Link to="/input" className="Header-link-item">
                            <p className="Header-item" onClick={toggleCategoryMenu}>Cuenta</p>
                        </Link>
                    }
                    {
                        customerSession &&
                        <Link to="/input" className="Header-link-item">
                            <p className="Header-item" onClick={closeSessionCustomerClick}>Cerrar sesin</p>
                        </Link>
                    }
                </nav>

                {/* --------------- ICON LEFT MENU CATEGORIES-------------- */}
                <AiOutlineMenu className='Header-icon-burguer' onClick={toggleCategoryMenu} />

                {/* ---------------- LOGO ---------------- */}
                <Link to='/'>
                    <img className='HeaderImg' src={logo} alt="logo" />
                </Link>

                { //------------- ICONS CUSTOMERS only if not user in session ----------------//            
                    !userSession &&
                    <nav className="NavRight">
                        {
                            !customerSession &&
                            <Link to="/input">
                                <AiOutlineUser className="Header-icon-user" />
                            </Link>
                        }
                        <AiOutlineSearch className="Header-icon-search" onClick={iconFinderClick} />
                        <Link to="/carrito">
                            <div className="Header-container-cart">
                                <AiOutlineShoppingCart className="Header-icon-cart" />
                                {
                                    articlesCart.length > 0 &&
                                    <span>{articlesCart.length}</span>
                                }
                            </div>
                        </Link>
                        {
                            customerSession &&
                            <>
                                <CgUserList className="Header-icon-menu-customer" onClick={toggleCustomerMenu} />
                                <span className="Header-customeremail">{customerSession['email']}</span>
                            </>
                        }
                    </nav>
                }

                { //----------- username and icon user menu;  only if exists session -----------//           
                    userSession &&
                    <>
                        <CgUserList className='Header-icon-burguer' onClick={toggleUserMenu} />
                        <span className="Header-username">{userSession['userName']}</span>
                    </>
                }

                {/* --------------------- RIGHT SIDEBAR USER MENU ---------------------------------- */}
                <nav className={isUserMenu ? 'Header-menu-user active' : 'Header-menu-user'}>
                    <AiOutlineClose className="Header-icon-close" onClick={toggleUserMenu} />
                    {
                        userSession &&
                        <p>{userSession['userName']}</p>
                    }
                    <ul className="Header-list">
                        <Link to="/categories" className="Header-link-item">
                            <li className="Header-item" onClick={toggleUserMenu}>CATEGORIAS</li>
                        </Link>
                        <hr />
                        <Link to="/articles" className="Header-link-item">
                            <li className="Header-item" onClick={toggleUserMenu}>ARTCULOS</li>
                        </Link>
                        <hr />
                        <Link to="/customers" className="Header-link-item">
                            <li className="Header-item" onClick={toggleUserMenu}>CLIENTES</li>
                        </Link>
                        <hr />
                        <Link to="/orders" className="Header-link-item">
                            <li className="Header-item" onClick={toggleUserMenu}>PEDIDOS</li>
                        </Link>
                    </ul>
                    <hr />

                    { //------------ only if user is super-admin ---------------//
                        userSession && userSession['id'] === 0 &&
                        <Link to="/users" className="Header-link-item">
                            <p className="Header-item" onClick={toggleUserMenu}>Usuarios</p>
                        </Link>
                    }
                    <Link to="/user-pass" className="Header-link-item">
                        <p className="Header-item" onClick={toggleUserMenu}>Cambiar contrasea</p>
                    </Link>
                    <Link to="/" className="Header-link-item">
                        <p className="Header-item" onClick={closeSessionClick}>Cerrar sesin</p>
                    </Link>
                </nav>

                {/* --------------------- RIGHT SIDEBAR CUSTOMER MENU ---------------------------------- */}
                <nav className={isCustomerMenu ? 'Header-menu-customer active' : 'Header-menu-customer'}>
                    <AiOutlineClose className="Header-icon-close" onClick={toggleCustomerMenu} />
                    {
                        customerSession &&
                        <p>{customerSession['email']}</p>
                    }
                    <ul className="Header-list">
                        <Link to="/customer-orders" className="Header-link-item">
                            <li className="Header-item" onClick={toggleCustomerMenu}>MIS PEDIDOS</li>
                        </Link>
                        <hr />
                        <Link to="#" className="Header-link-item">
                            <li className="Header-item" onClick={toggleCustomerMenu}>ESTADO DE CUENTA</li>
                        </Link>
                    </ul>
                    <hr />

                    {/* --------------------- DATA CUSTOMER AND CLOSE SESSION ---------------------- */}
                    <Link to="/customer-data" className="Header-link-item">
                        <p className="Header-item" onClick={toggleCustomerMenu}>Mis datos</p>
                    </Link>
                    <Link to="/customer-pass" className="Header-link-item">
                        <p className="Header-item" onClick={toggleCustomerMenu}>Cambiar contrasea</p>
                    </Link>
                    <Link to="/input" className="Header-link-item">
                        <p className="Header-item" onClick={closeSessionCustomerClick}>Cerrar sesin</p>
                    </Link>
                </nav>
            </header>
            {
                isShowFinder &&
                <section className='Header-finder'>
                    <AiOutlineSearch className="Header-finder-icon" />
                    <input
                        type="search"
                        name="search"
                        placeholder='BUSCAR...'
                        value={txtSearch}
                        onChange={inputChange}
                        autoFocus={isShowFinder}
                    />
                    <AiOutlineCloseCircle className="Header-finder-icon close" onClick={iconCloseFinderClick} />
                </section>
            }
        </>
    );
}