// React
import { useState, useEffect, useContext } from 'react';
import { useNavigate, useParams } from 'react-router-dom'
// components
import FormMessage from '../form/FormMessage';
import BtnPrimary from '../buttons/BtnPrimary';
import BtnSecundary from '../buttons/BtnSecundary';
// context
import { DataContext } from '../../context/DataContext';

export default function CategoryForm() {
    // context
    const { fetchGet, fetchPost, fetchPut, userSession } = useContext(DataContext);

    // states
    const [category, setCategory] = useState({
        id: -1, // for verify if email exists
        categoryName: '',
        isAvailable: true
    });
    const [msg, setMsg] = useState('');

    // read param
    const params = useParams(); // important!
    const id = params['id'];

    const navigate = useNavigate();

    useEffect(() => {
        //console.log('useEffect UserForm');

        async function loadCategory(id) {
            const json = await fetchGet('/category.php?fn=getCategory&id=' + id, userSession['token']);
            //console.log(json);
            if (json !== null) setCategory(json['category']);            
        }

        if (userSession) {
            if (id) loadCategory(id);
        }
        else navigate('/not-found')

    }, [id, fetchGet, navigate, userSession, msg]);
    
    const inputChange = (e) => {
        //console.log(e.target);
        const { name, value } = e.target; // extract name of Element and value
        //console.log('category: ', category);
        setCategory({
            ...category, // copy object (same values)
            [name]: name !== 'isAvailable' ? value : e.target.checked // only change Element named 'name'
        });
    }

    const formSubmit = async (e) => {
        e.preventDefault();
        //console.log('category before save: ', category);

        // verify if categoryName exists
        let json = await fetchGet(`/category.php?fn=isExistsCategory&categoryName=${category['categoryName']}&id=${category['id']}`, userSession['token']);
        //console.log(res);                

        if (!json['isExistsCategory']) {
            //console.log('id before update-insert: ', category['id']);

            // update
            if (category['id'] > -1) {
                //console.log('before update: ', category);

                json = await fetchPut('/category.php?fn=updateCategory',
                    category,
                    userSession['token']);
                //console.log(json);

            }
            // insert
            else {
                json = await fetchPost('/category.php?fn=insertCategory',
                    category,
                    userSession['token']);
                //console.log('json: ', json);                   
            }
            navigate('/categories');
        }
        else setMsg('La categoria ya existe');

    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <div className="Card">                
                <h4>{id ? 'Modificando categora' : 'Agregando categora'}</h4>
                <form className='Form-user' onSubmit={formSubmit}>
                    <label htmlFor="categoryName">Descripcin</label>
                    <input
                        type="text"
                        id="categoryName"
                        name="categoryName"
                        value={category.categoryName}
                        onChange={inputChange}
                        autoFocus
                        required
                    />
                    <label htmlFor="isAvailable">Disponible</label>
                    <input
                        type="checkbox"
                        id='isAvailable'
                        name="isAvailable"
                        checked={category.isAvailable}
                        onChange={inputChange}
                    />
                    <div className='Form-user-actions'>
                        <BtnPrimary text='Guardar' type='submit' />
                        <BtnSecundary text='Cancelar' onClick={() => navigate('/categories')} />
                    </div>

                    { // show only if msg !== ''
                        msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                    }

                </form>
            </div>
        </div>
    )
}