// React
import { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom'
// components
import FormMessage from '../form/FormMessage';
import BtnPrimary from '../buttons/BtnPrimary';
import BtnSecundary from '../buttons/BtnSecundary';
// context
import { DataContext } from '../../context/DataContext';
// css
import './UserForm.css';

export default function UserForm({ id }) {
    // context
    const { fetchGet, fetchPost, fetchPut, userSession } = useContext(DataContext);
    // states
    const [user, setUser] = useState({
        id: -1, // for verify if email exists
        userName: '',
        email: '',
        isAdmin: false,
        isActive: true,
        pass: '',
        confirm: ''
    });
    const [isShowPass, setIsShowPass] = useState(false)
    const [msg, setMsg] = useState('');
    // navigate
    const navigate = useNavigate();

    useEffect(() => {
        //console.log('Effect UserForm');

        async function loadUser(id) {
            let data = await fetchGet('/user.php?fn=getUser&id=' + id, userSession['token']);
            //console.log(data);

            //data['isAuth'] = false; // test for close session
            if (data !== null) {
                data = data['user'];
                data['pass'] = '';
                data['confirm'] = '';
                setUser(data);
            }            
        }

        if (userSession) {
            if (id) loadUser(id);
            else setIsShowPass(true);
        }
        else navigate('/not-found')

        return () => { 
            //console.log('Effect UserForm BYE') 
        }
    }, [id, userSession, msg, fetchGet, navigate]);

    const inputChange = (e) => {
        //console.log(e.target);
        const { name, value } = e.target; // extract name of Element and value
        //console.log('user: ', user);
        setUser({
            ...user, // copy object (same values)
            [name]: name !== 'isAdmin' && name !== 'isActive' ? value : e.target.checked // only change Element named 'name'
        });
    }

    const toggleShowPass = (e) => {
        //console.log(e.target.checked);
        setIsShowPass(e.target.checked);
    }

    const formSubmit = async (e) => {
        e.preventDefault();
        //console.log('user before save: ', user);

        // verify if email exists
        let json = await fetchGet(`/user.php?fn=isExistsEmail&email=${user['email']}&id=${user['id']}`, userSession['token']);
        //console.log(json);

        if (json !== null) {
            if (!json['isExistsEmail']) {

                // verify if pass = confirm
                if (user['pass'] === user['confirm']) {

                    // update
                    if (id) {
                        json = await fetchPut('/user.php?fn=updateUser',
                            user,
                            userSession['token']);
                        //console.log(json);
                    }
                    // insert
                    else {
                        json = await fetchPost('/user.php?fn=insertUser',
                            user,
                            userSession['token']);
                        //console.log(json);
                    }
                    navigate('/users');
                }
                else setMsg('La contrasea y su confirmacin son diferentes');
            }
            else setMsg('El correo ya existe');
        }
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        userSession &&
        <div className="Card">            
            <h4>{id ? 'Modificando usuario' : 'Agregando usuario'}</h4>
            <form className='Form-user' onSubmit={formSubmit}>
                <label htmlFor="userName">Usuario</label>
                <input
                    type="text"
                    id='userName'
                    name="userName"
                    value={user.userName}
                    onChange={inputChange}
                    autoFocus
                    required />
                <label htmlFor="email">Correo</label>
                <input
                    type="email"
                    id='email'
                    name="email"
                    value={user.email}
                    onChange={inputChange}
                    required />
                <label htmlFor="isAdmin">Administrador</label>
                <input
                    type="checkbox"
                    id='isAdmin'
                    name="isAdmin"
                    checked={user.isAdmin}
                    onChange={inputChange}
                    disabled={user.id === 0}
                />
                <label htmlFor="isActive">Activo</label>
                <input
                    type="checkbox"
                    id='isActive'
                    name="isActive"
                    checked={user.isActive}
                    onChange={inputChange}
                    disabled={user.id === 0}
                />
                {  // show only if editing
                    id &&
                    <>
                        <label htmlFor="password">Cambiar contrasea</label>
                        <input
                            type="checkbox"
                            id="isShowPass"
                            onChange={toggleShowPass}
                        />
                    </>
                }
                { // show only if above checkbox is checked
                    isShowPass &&
                    <div className='Form-user-pair-pass'>
                        <div>
                            <label htmlFor="password">Contrasea</label>
                            <input
                                type="password"
                                name="pass"
                                value={user.pass}
                                onChange={inputChange}
                                required={id || isShowPass /* with id or user-editing with checked-showPass*/}
                            />
                        </div>
                        <div>
                            <label htmlFor="confirm">Confirmar</label>
                            <input
                                type="password"
                                name="confirm"
                                value={user.confirm}
                                onChange={inputChange}
                                required={id || isShowPass /* with id or user-editing with checked-showPass */}
                            />
                        </div>
                    </div>
                }

                <div className='Form-user-actions'>
                    <BtnPrimary text='Guardar' type='submit' />
                    <BtnSecundary text='Cancelar' onClick={() => navigate('/users')} />
                </div>

                { // show only if msg !== ''
                    msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                }

            </form>
        </div>
    )
}