import React from 'react'

const BtnPrimary = ({ text, onClick, type = 'button'}) => {
  return (
    <button type={type}
      className='Btn'
      onClick={onClick}  
      style={{
        color: 'white',
        backgroundColor: 'dodgerblue'
      }}    
    >{text}
    </button>
  )
}

export default BtnPrimary