// components
import BtnSecundary from '../buttons/BtnSecundary';
// css
import './ModalInformation.css';

const ModalInformation = ({ isShowModal, subject, btnCloseClick }) => {
    
  return (
    <div className={isShowModal ? 'Modal-information active' : 'Modal-information'}>
      <div className='Modal-card'>
        <h3>Eliminar</h3>
        <p>{subject}</p>
        <div className='Modal-actions'>
          <BtnSecundary text='Cerrar' onClick={btnCloseClick} />
        </div>
      </div>
    </div>
  )
}

export default ModalInformation