// react
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
// context
import { DataProvider } from './context/DataContext';
// components
import Header from "./components/header/Header";
import Welcome from './components/welcome/Welcome';
import UserInput from './components/users/UserInput';
import UsersList from "./components/users/UsersList";
import UserInsert from "./components/users/UserInsert";
import UserUpdate from "./components/users/UserUpdate";
import UserChangePass from "./components/users/UserChangePass";
import CustomerInput from './components/customers/CustomerInput';
import Categories from './components/categories/Categories';
import CategoryForm from './components/categories/CategoryForm';
import ArticlesList from './components/articles/ArticlesList';
import ArticleForm from './components/articles/ArticleForm';
import Products from './components/products/Products';
import ProductDetail from './components/products/ProductDetail';
import Customers from './components/customers/CustomersList';
import CustomerForm from './components/customers/CustomerForm';
import Cart from './components/cart/Cart';
import Footer from './components/footer/Footer';
import CustomerOrdersList from './components/customer_authorized/CustomerOrdersList';
import CustomerPass from './components/customer_authorized/CustomerPass';
import CustomerData from './components/customer_authorized/CustomerData';
import OrdersList from './components/orders/OrdersList';
import NotFound from './components/not_found/NotFound';
import CustomerRegister from './components/customers/CustomerRegister';
import CustomerActivation from './components/customers/CustomerActivation';
import CustomerPassLost from './components/customers/CustomerPassLost';
import CustomerPassRestore from './components/customers/CustomerPassRestore';

export default function App() {

    return (
        <div className='Body-wrapper'>
            <Router>
                <DataProvider > {/* contain useNavigate, only into Router */}
                    <Header />
                    <main className='Main'>
                        <Routes>
                            <Route path="/" element={<Welcome />} />
                            {/* <Route path="/products/:name" element={<Products />} /> */}
                            {/* --------------------- CATALOG ------------------------ */}
                            {/* <Route path="/productos/:txtSearch/:isCategory" element={<Products />} /> */}
                            <Route path="/productos/:search/:txtSearch" element={<Products />} />
                            <Route path="/producto-detalle/:id" element={<ProductDetail />} />
                            {/* ---------------- INPUT CUSTOMER OR USER ------------- */}
                            <Route path="/input" element={<CustomerInput />} />
                            <Route path="/admin" element={<UserInput />} />
                            {/* ----------------------- USER ------------------------ */}
                            <Route path="/users" element={<UsersList />} />
                            <Route path="/user-insert" element={<UserInsert />} />
                            <Route path="/user-update/:id" element={<UserUpdate />} />
                            <Route path="/user-pass" element={<UserChangePass />} />
                            {/* ---------------------- CATEGORIES --------------------- */}
                            <Route path="/categories" element={<Categories />} />
                            <Route path="/category-insert" element={<CategoryForm />} />
                            <Route path="/category-update/:id" element={<CategoryForm />} />
                            {/* ----------------------- ARTICLES ---------------------- */}
                            <Route path="/articles" element={<ArticlesList />} />
                            <Route path="/article-insert" element={<ArticleForm />} />
                            <Route path="/article-update/:id" element={<ArticleForm />} />
                            {/* ----------------------- ORDERS ------------------------ */}
                            <Route path="/orders" element={<OrdersList />} />
                            {/* ------------------- CUSTOMERS ------------------------- */}
                            <Route path='/customers' element={<Customers />} />
                            <Route path="/customer-insert" element={<CustomerForm />} />
                            <Route path="/customer-update/:id" element={<CustomerForm />} />
                            <Route path="/customer-register" element={<CustomerRegister />} />
                            <Route path="/customer-activation/:token" element={<CustomerActivation />} />
                            <Route path="/customer-pass-lost" element={<CustomerPassLost />} />
                            <Route path="/customer-pass-restore/:token" element={<CustomerPassRestore />} />
                            {/* -------------- CUSTOMER AUTHORIZED -------------------- */}
                            <Route path="/carrito" element={<Cart />} />
                            <Route path="/customer-orders" element={<CustomerOrdersList />} />
                            <Route path="/customer-pass" element={<CustomerPass />} />
                            <Route path="/customer-data" element={<CustomerData />} />
                            {/* ---------------- PAGE NOT-FOUND ----------------------- */}
                            <Route path="/*" element={<NotFound />} />
                            <Route path="/not-found" element={<NotFound />} />
                        </Routes>
                    </main>
                    <Footer />
                </DataProvider>
            </Router>
        </div>
    );
}