import React, { useContext, useEffect } from 'react'

// components
import FormCustomerData from '../form/FormCustomerData'

// context
import { DataContext } from '../../context/DataContext'
import { useNavigate } from 'react-router'

const CustomerData = () => {
  const { customerSession } = useContext(DataContext) 

  const navigate = useNavigate()

  useEffect(() => {
    if (customerSession === null) navigate('/not-found')    
    return () => {}
  }, [customerSession, navigate])  

  return (
    customerSession &&
    <>
      <p className='ProductsTitle'>MIS DATOS</p>
      <div className='Wrapper-flex'>
        <FormCustomerData isAutoRegister={false} />
      </div>
    </>
  )
}

export default CustomerData