import { useNavigate } from 'react-router-dom';
import { useState, useEffect, useContext } from "react";
// context
import { DataContext } from '../../context/DataContext';
// components
import FormMessage from "../form/FormMessage";

export default function UserChangePass() {

    // context
    const { userSession, fetchPost } = useContext(DataContext);

    const navigate = useNavigate();

    const [inputsTxt, setInputsTxt] = useState({
        current: '',
        pass: '',
        confirm: '',
        isPassChanged: false
    });

    const [msg, setMsg] = useState('');

    // only first load
    useEffect(() => {
        //console.log('useEffect UserChangePass');
        if (!userSession) navigate('/not-found');
    });

    const showFormMessage = (msg) => {
        setMsg(msg);
    }

    const formOnSubmit = async (e) => {
        e.preventDefault();

        // verify current pass
        let res = await fetchPost(
            'user.php?fn=isPassOk',
            {
                pass: inputsTxt.current,
                id: userSession.id
            },
            userSession.token
        );
        //console.log(res);
        let data = JSON.parse(res);
        if (data !== null) {
            setMsg('');
            if (data['isPassOk']) {
                // verify if new pass = confirm
                if (inputsTxt.pass === inputsTxt.confirm) {
                    res = await fetchPost(
                        'user.php?fn=setUserPass',
                        {
                            pass: inputsTxt.pass,
                            id: userSession.id
                        },
                        userSession.token
                    );
                    //console.log(res);
                    data = JSON.parse(res);
                    if (data !== null) {
                        if (data['isPassChanged']) {
                            setInputsTxt({
                                current: '',
                                pass: '',
                                confirm: '',
                                isPassChanged: true
                            });
                            setMsg('Contrasea ha sido actualizada');
                        }
                    }
                }
                else showFormMessage('Nueva contrasea y su confirmacin son diferentes');
            }
            else showFormMessage('Contrasea actual incorrecta');
        }
    }

    const txtOnChange = (e) => {
        //console.log(e.target.value);        
        setInputsTxt({
            ...inputsTxt, // copy same data
            [e.target.name]: e.target.value // only update input changued
        });
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>CAMBIAR CONTRASEA DE USUARIO</h3>
                { // only if isPassChange = false
                    !inputsTxt.isPassChanged &&
                    <form onSubmit={formOnSubmit}>
                        <input type="password" name="current" placeholder='Contrasea actual' onChange={txtOnChange} required />
                        <input type="password" name="pass" placeholder='Nueva contrasea' onChange={txtOnChange} required />
                        <input type="password" name="confirm" placeholder='Confirmar nueva contrasea' onChange={txtOnChange} required />
                        <input type="submit" value="CAMBIAR" style={{backgroundColor: 'dodgerblue', borderRadius: '5px'}}/>
                    </form>

                }
                {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} isSuccess={inputsTxt.isPassChanged} />}
            </div>
        </div>
    );
}