// components
import BtnDanger from '../buttons/BtnDanger';
import BtnSecundary from '../buttons/BtnSecundary';
// css
import './ModalDelete.css';

const ModalDelete = ({ isShowModal, subject, btnDeleteClick, btnCancelClick }) => {
    
  return (
    <div className={isShowModal ? 'Modal-delete active' : 'Modal-delete'}>
      <div className='Modal-card'>
        <h3>Eliminar</h3>
        <p>{subject}</p>
        <div className='Modal-actions'>
          <BtnDanger text='Eliminar' onClick={btnDeleteClick}/>
          <BtnSecundary text='Cancelar' onClick={btnCancelClick} />
        </div>
      </div>
    </div>
  )
}

export default ModalDelete