import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
// context
import { DataContext } from '../../context/DataContext'
// components
import FormMessage from '../form/FormMessage'

const CustomerPassRestore = () => {
    // context
    const { customerSession, fetchGet, fetchPut } = useContext(DataContext)
    // params
    const params = useParams()
    // states
    const [idCustomer, setIdCustomer] = useState(-1)
    const [inputs, setInputs] = useState({
        pass: '',
        confirm: ''
    })
    const [formMessage, setFormMessage] = useState({
        msg: '',
        isSuccess: false
    })
    // navigate
    const navigate = useNavigate()

    useEffect(() => {
        //console.log(params)
        window.history.replaceState(null, '', '/customer-pass-restore')

        async function getIdCustomerFromToken() {
            // get idCustomer from token (-1 not exists)
            let res = await fetchGet(`/index.php?fn=getIdCustomerFromToken&token=${params['token']}`)
            //console.log(res) // null|{idCustomer: <int>}
            if (res !== null) {
                setIdCustomer(res['idCustomer'])

                // verify if idCustomer > -1
                if (res['idCustomer'] > -1) {
                    setFormMessage({
                        msg: '',
                        isSuccess: false
                    })
                }
                else {
                    setFormMessage({
                        msg: 'El link para restaurar tu contrasea\nno es vlido.',
                        isSuccess: false
                    })                    
                }
            }
        }

        if (!customerSession && params['token']) getIdCustomerFromToken()
        else navigate('/not-found')
    }, [customerSession, params, navigate, fetchGet, fetchPut])

    function inputChange(e) {
        setInputs({
            ...inputs, // same values
            [e.target.name]: e.target.value // only change 1 value named equals that e.target.name 
        })
    }

    async function onSubmit(e) {
        e.preventDefault()

        // verify if pass and confirm are equals
        if (inputs.pass === inputs.confirm) {
            // update new pass
            let res = await fetchPut(
                '/index.php?fn=changePassCustomer',
                {
                    id: idCustomer,
                    pass: inputs.pass
                }
            )
            //console.log(res) // null|{}
            if (res !== null) {
                setInputs({
                    pass: '',
                    confirm: ''
                })
                setFormMessage({
                    msg: 'Tu contrasea ha sido restaurada.',
                    isSuccess: true
                })
            }
        }
        else {
            setInputs({
                pass: '',
                confirm: ''
            })
            setFormMessage({
                msg: 'Las contraseas son diferentes.',
                isSuccess: false
            })
        }

    }

    return (
        !customerSession && params['token'] &&
        <>
            <p className='ProductsTitle'>RESTAURAR CONTRASEA</p>
            <div className='Wrapper-flex'>
                <div className='FormCard'>
                    {
                        idCustomer > -1 && !formMessage.isSuccess &&
                        <form onSubmit={onSubmit}>
                            <input
                                type='password'
                                name='pass'
                                placeholder='Nueva contrasea'
                                value={inputs.pass}
                                onChange={inputChange}
                                required
                            />
                            <input
                                type='password'
                                name='confirm'
                                placeholder='Confirmar nueva contrasea'
                                value={inputs.confirm}
                                onChange={inputChange}
                                required
                            />
                            <input
                                type='submit'
                                value='Aceptar'
                            />
                        </form>
                    }
                    {
                        formMessage.msg.length > 0 &&
                        <FormMessage msg={formMessage.msg} isSuccess={formMessage.isSuccess} />
                    }
                    {
                        formMessage.isSuccess &&
                        <Link to='/input'>
                            <p>Iniciar sesin</p>
                        </Link>
                    } 
                </div>
            </div>
        </>
    )
}

export default CustomerPassRestore