import React from 'react'

const BtnAccept = ({ text, onClick, type = 'button'}) => {
  return (
    <button type={type}
      className='Btn Btn-accept'
      onClick={onClick}  
      style={{
        color: 'white',
        backgroundColor: 'rgb(0, 187, 0)'
      }}    
    >{text}
    </button>
  )
}

export default BtnAccept