import { useCallback, useEffect, useState, useContext } from 'react';
// components
import BtnSecundary from '../buttons/BtnSecundary';
// context
import { DataContext } from '../../context/DataContext'
// css
import './ModalAddArticle.css';

const ModalAddArticle = ({ isShowModal, idOrder, btnAcceptClick, btnCancelClick }) => {
  // context
  const { fetchGet, userSession } = useContext(DataContext)
  // states
  const [categories, setCategories] = useState([])
  const [categoryNameSelected, setCategoryNameSelected] = useState('')
  const [articles, setArticles] = useState([])

  const loadCategories = useCallback(async () => {
    let res = await fetchGet('/category.php?fn=getAllCategories', userSession['token'])
    if (res !== null) {
      res = res['categories']
      setCategories(res)
      setCategoryNameSelected(res[0].categoryName) // first category
    }
  }, [fetchGet, userSession])

  // effect init
  useEffect(() => {
    if (userSession) loadCategories()
    return () => { }
  }, [loadCategories, userSession])

  const loadArticles = useCallback(async () => {
    //console.log('loadArticles()')
    const res = await fetchGet(
      `/article.php?fn=getAllArticlesFromCategoryName&categoryName=${categoryNameSelected}`,
      userSession['token']
    )
    if (res !== null) setArticles(res['articles'])
  }, [fetchGet, categoryNameSelected, userSession])

  // effect categoryNameSelected
  useEffect(() => {
    if (categoryNameSelected !== '') loadArticles()
    return () => { }
  }, [categoryNameSelected, loadArticles])

  function selectChange(e) {
    //console.log(e.target.value)
    setCategoryNameSelected(e.target.value)
  }

  return (
    userSession &&
    <div className={isShowModal ? 'ModalAddArticle-background active' : 'ModalAddArticle-background'}>
      <div className='ModalAddArticle-card'>
        <h3>Agregar artculo al pedido: {idOrder}</h3>
        <section className='ModalAddArticle-section-select'>
          <label htmlFor="select">Categora </label>
          <select
            name="select"
            id="select"
            onChange={selectChange}
          >
            {
              categories.map((category, i) =>
                <option key={i} value={category.categoryName}>{category.categoryName}</option>
              )
            }
          </select>
        </section>
        <table className='ModalAddArticle-table'>
          <thead>
            <tr>
              <th>ID</th>
              <th>Descripcin</th>
              <th>Precio</th>
              <th>Exist</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {
              articles.map((article, i) =>
                <tr key={i}>
                  <td className='ModalAddArticle-col-number'>{article.id}</td>
                  <td style={{width: '50%'}}>{article.articleName}</td>
                  <td className='ModalAddArticle-col-number'>${article.price}</td>
                  <td className='ModalAddArticle-col-number'>{article.stock}</td>
                  <td><button className='ModalAddArticle-btn-add' onClick={() => btnAcceptClick(article.id)}>Agregar</button></td>
                </tr>
              )
            }
          </tbody>
        </table>
        <div className='ModalAddArticle-actions'>
          <BtnSecundary text='Cancelar' onClick={btnCancelClick} />
        </div>
      </div>
    </div>
  )
}

export default ModalAddArticle