import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router';
import { Link } from 'react-router-dom'
// context
import { DataContext } from '../../context/DataContext';
// icon
import { AiFillDelete, AiOutlineMinus, AiOutlinePlus } from 'react-icons/ai';
// components
import FormMessage from '../form/FormMessage';
// css
import './Cart.css';

const Cart = () => {
  // context
  const {
    fetchGet,
    fetchPost,
    articlesCart,
    setArticlesCart,
    totalCart,
    setTotalCart,
    customerSession,
    setIsReturnToCart
  } = useContext(DataContext);

  // useNavigate
  const navigate = useNavigate()

  // states
  const [msg, setMsg] = useState('')
  const [idOrder, setIdOrder] = useState(-1)

  // vars
  var articleWithoutStock = ''

  useEffect(() => {
    //console.log('>>> useEffect Cart');
    //console.log('articlesCart: ', articlesCart);

    // update total
    let newTotal = articlesCart.reduce((total, articleCart) =>
      total = total + articleCart['qty'] * articleCart['price'], 0) // this zero its initial
    //console.log('total: ', newTotal)
    setTotalCart(newTotal)

    // save articlesCart on storage
    window.localStorage.setItem('cart', JSON.stringify(articlesCart))

  }, [articlesCart, setTotalCart]);

  const substractClick = (id) => {
    //console.log('substractClick: ' + id);
    // find index of article in cart
    const i = articlesCart.findIndex(article => article.id === id)
    //console.log('i: ', i)

    // if exists
    if (i > -1) {
      // if current qty >= 2
      if (articlesCart[i].qty >= 2) {
        const updateArticlesCart = articlesCart.map(articleCart =>
          articleCart === articlesCart[i] // same article?
            ? { ...articleCart, qty: articleCart['qty'] - 1 } // copy article, only change qty
            : articleCart // if not exists, return same object
        )
        setArticlesCart(updateArticlesCart)
      }
    }
  }

  const addClick = (id) => {
    //console.log('addClick: ' + id);
    //console.log('articlesCart: ', articlesCart);

    // find index of article in cart
    const i = articlesCart.findIndex(article => article.id === id)
    //console.log('i: ', i)

    if (i > -1) {
      const updateArticlesCart = articlesCart.map(articleCart =>
        articleCart === articlesCart[i] // same article?
          ? { ...articleCart, qty: articleCart['qty'] + 1 } // copy article, only change qty          
          : articleCart // same object
      )
      setArticlesCart(updateArticlesCart)
    }
  }

  const deleteClick = (id) => {
    //console.log('deleteClick: ' + id);
    setArticlesCart(articlesCart.filter(articleCart => articleCart.id !== id));
  }

  //---------------- SUMMARY -----------------//
  function cleanCartClick() {
    setArticlesCart([])
    setTotalCart(0)
  }

  function btnStartSessionClick() {
    setIsReturnToCart(true)
    navigate('/input')
  }

  async function btnOrderClick() {
    // verify if complete qty on database
    let isContinue = true
    articlesCart.forEach(async (articleCart, i) => {
      //console.log(articleCart, i)
      let res = await fetchGet(`/customer_authorized.php?fn=isStockComplete&id=${articleCart.id}&qty=${articleCart.qty}`, customerSession.token)
      //console.log(res); // {isAuth: true|false, isStockComplete: true|false}
      if (res !== null) {
        if (!res['isStockComplete']) {
          isContinue = false
          articleWithoutStock = articleCart.articleName
        }

        // last item
        if (i === articlesCart.length - 1) {
          if (isContinue) {
            setMsg('')
            //console.log('save order!!')

            // insert order from cart
            res = await fetchPost(
              `/customer_authorized.php?fn=insertOrderFromCart&items=${articlesCart.length}&total=${totalCart}`,
              articlesCart,
              customerSession.token
            )
            //console.log(res) // {isAuth: true|false, idOrder: <int>}

            setIdOrder(res['idOrder']);

            // clear cart
            setArticlesCart([]);
          }
          else {
            setMsg(`Existencia insuficiente:\n${articleWithoutStock}`)
          }
        }
      }
    });
  }

  function onCloseFormMessage() {
    setMsg('')
  }

  function onCloseFormMessageOrder() {
    setIdOrder(-1)
  }

  return (
    <>
      {/*console.log('final articlesCart: ', articlesCart)*/}
      {/* <div className='Cart-top'>Carrito</div> */}
      <div className='Cart-title'>Carrito</div>

      <div className='Cart-container'>
        <div className='Cart-table-summary'>
          <table className='Cart-table'>
            <thead>
              <tr className='Cart-row Cart-row-delete'>
                <th className='Cart-col-product'>Producto</th>
                <th className='Cart-col-qty'>Cantidad</th>
                <th className='Cart-col-price'>Precio</th>
                <th className='Cart-col-amount'>Importe</th>
              </tr>
            </thead>
            <tbody>
              {
                articlesCart.map((articleCart, i) =>
                  <tr className='Cart-row no-title' key={i}>
                    <td className='Cart-col-product'>
                      <div className='Cart-container-img'>
                        <img src={articleCart.imgPath} alt={articleCart.articleName} />
                      </div>
                      <Link to={'/productoDetalle/' + articleCart.id} className='Cart-description'>
                        {articleCart.articleName}
                      </Link>
                    </td>
                    <td className='Cart-col-qty'>
                      <div className='Cart-qty'>
                        <AiOutlineMinus className='Cart-icon' onClick={() => substractClick(articlesCart[i].id)} />
                        <span>{articleCart.qty}</span>
                        <AiOutlinePlus className='Cart-icon' onClick={() => addClick(articlesCart[i].id)} />
                      </div>
                    </td>
                    <td className='Cart-col-price'>${new Intl.NumberFormat('es-MX').format(articleCart.price)}</td>
                    <td className='Cart-col-amount'>
                      ${new Intl.NumberFormat('es-MX').format(articleCart.qty * articleCart.price)}
                      <AiFillDelete className='Cart-icon Cart-icon-delete' onClick={() => deleteClick(articlesCart[i].id)} />
                    </td>
                  </tr>
                )
              }
            </tbody>
          </table>

          {/* ---------------- SUMMARY CARD ------------------- */}
          <div className='Cart-summary'>
            <div className='Card'>
              <h3>RESUMEN</h3>
              <br />
              {idOrder === -1 ?
                <>
                  <code>{articlesCart.length} {articlesCart === 1 ? 'artculo' : 'artculos'}</code>
                  <h4>Total ${new Intl.NumberFormat('es-MX', { minimumFractionDigits: 2 }).format(totalCart)}</h4>
                  {
                    articlesCart.length > 0 &&
                    <>
                      {customerSession
                        ? <button className='Cart-btn-order' onClick={btnOrderClick}>ORDENAR</button>
                        : <button className='Cart-btn-order' onClick={btnStartSessionClick}>INICIAR SESIN PARA ORDENAR</button>
                      }
                      <br />
                      <br />
                      <p className='Cart-clean-cart' onClick={cleanCartClick}>Limpiar carrito</p>
                      {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />}
                    </>
                  }
                </>
                : <FormMessage msg={'rden enviada con el folio: ' + idOrder} btnCloseClick={onCloseFormMessageOrder} isSuccess={true} />
              }
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Cart