import React, { useContext, useEffect, useState, useCallback } from 'react'
import { useNavigate } from 'react-router';

// component
import BtnDanger from '../buttons/BtnDanger'
import BtnPrimary from '../buttons/BtnPrimary'
import BtnAccept from '../buttons/BtnAccept'
import ModalDelete from '../modal_delete/ModalDelete'

// icon
import { AiFillDelete, AiOutlinePlus, AiOutlineMinus } from 'react-icons/ai';

// context
import { DataContext } from '../../context/DataContext';

// css
import './OrdersList.css'
import ModalAddArticle from '../modal_add_article/ModalAddArticle';

const OrdersList = () => {
    // context
    const { fetchGet, fetchPost, fetchPut, fetchDelete, userSession } = useContext(DataContext)

    // states
    const [orders, setOrders] = useState([])
    const [idOrderSelected, setIdOrderSelected] = useState(-1)
    const [indexOrderSelected, setIndexOrderSelected] = useState(-1)
    const [indexCustomerSelected, setIndexCustomerSelected] = useState(-1)
    const [itemsOrder, setItemsOrder] = useState([])
    const [stateModalDelete, setStateModalDelete] = useState({
        isShowModal: false,
        isDeleteOrder: false,
        isRestore: false,
        subject: '',
        id: -1
    })
    const [customers, setCustomers] = useState([])
    const [dates, setDates] = useState({
        firstDate: new Date(Date.now() - 1000 * 60 * 60 * 24 * 10/*<- last number is days*/).toLocaleDateString('en-CA'), // yyyy-MM-dd
        lastDate: new Date().toLocaleDateString('en-CA') // yyyy-MM-dd
    })
    const [stateModalAddArticle, setStateModalAddArticle] = useState({
        isShowModal: false,
        idOrder: -1
    })

    // navigate
    const navigate = useNavigate()

    const loadItemsOrder = useCallback(async (idOrder) => {
        const res = await fetchGet(`/customer_authorized.php?fn=getItemsOrder&idOrder=${idOrder}`)
        //console.log(res)

        if (res !== null) setItemsOrder(res['itemsOrder'])
    }, [fetchGet])

    const loadCustomers = useCallback(async () => {
        const res = await fetchGet('/customer.php?fn=getAllCustomers&txtSearch=', userSession.token)
        if (res !== null) setCustomers(res['customers'])        
    }, [fetchGet, userSession])

    const loadOrders = useCallback(async () => {
        const idCustomer = document.getElementById('select').value
        const res = await fetchGet(`/order.php?fn=getAllOrders&idCustomer=${idCustomer}&firstDate=${dates.firstDate}&lastDate=${dates.lastDate}`, userSession.token) // -1 = all orders
        //console.log(res);

        if (res !== null) {
            // update ordersList
            rowUnselect()
            setOrders(res['orders'])
            setItemsOrder([])
            //console.log('idOrderSelected after update orders: ', idOrderSelected)
        }        
    }, [fetchGet, userSession, dates.firstDate, dates.lastDate])


    useEffect(() => {
        //console.log('useEffect loadCustomers()')
        //console.log('firstDate: ', firstDate)
        if (userSession) loadCustomers()
        else navigate('/not-found')

        return () => { }
    }, [loadCustomers, navigate, userSession])

    useEffect(() => {
        //console.log('useEffect loadOrders()')

        if (userSession) loadOrders()
        else navigate('/not-found')

        return () => { }
    }, [userSession, navigate, loadOrders])

    function comboCustomersChange(e) {
        //console.log('comboCustomersChange', e.target.value)
        loadOrders()
    }

    function rowUnselect() {
        // clean selection
        let row = document.getElementsByClassName('active')[0];
        if (row) row.classList.remove('active');
        setIdOrderSelected(-1)
        setIndexOrderSelected(-1)
        setIndexCustomerSelected(-1)
    }

    function rowClick(e) {
        // console.log('idOrderSelected before rowClick', idOrderSelected);

        // only click over <td>
        if (e.target.tagName === 'TD') {
            rowUnselect()

            // get idOrder and select row
            const idOrder = parseInt(e.target.parentElement.id) // text
            setIdOrderSelected(idOrder)

            // get index-order-selected
            let i = orders.findIndex(order => order.id === idOrder)
            setIndexOrderSelected(i)

            // update index-customer-selected
            i = customers.findIndex(customer => customer.id === orders[i].idCustomer)
            //console.log('indexCustomerSelected: ', i)
            setIndexCustomerSelected(i)

            const row = document.getElementById(idOrder)
            row.classList.add('active')
            loadItemsOrder(idOrder)
        }
    }

    function btnDeleteRestoringClick(id) {
        //console.log('idOrderSelected before delete: ', idOrderSelected)
        setStateModalDelete({
            isShowModal: true, // show-Modal
            isDeleteOrder: true, // delete complete order
            isRestore: true, // restore stock
            subject: 'Ests segur@ de cancelar el pedido?',
            id: id // id to delete
        });
    }

    function btnDeleteClick(id) {
        //console.log('idOrderSelected before delete: ', idOrderSelected)
        setStateModalDelete({
            isShowModal: true, // show-Modal
            isDeleteOrder: true,
            isRestore: false, // no restore stock
            subject: 'Ests segur@ de eliminar el pedido?',
            id: id // id to delete
        });
    }

    async function iconDeleteClick(id) {
        //console.log('idOrderSelected before delete: ', idOrderSelected)
        setStateModalDelete({
            isShowModal: true, // show-Modal
            isDeleteOrder: false,
            isRestore: false, // only when isDeleteOrder
            subject: 'Ests segur@ de eliminar el artculo del pedido?',
            id: id // id to delete
        });
    }

    const onBtnDeleteClick = async () => {
        //console.log('idOrderSelected before onDelete: ', idOrderSelected);

        // delete full order
        let res
        if (stateModalDelete.isDeleteOrder) {
            let body
            if (stateModalDelete.isRestore)
                body = {
                    id: stateModalDelete.id,
                    isRestore: true
                }
            else
                body = {
                    id: stateModalDelete.id
                }
            res = await fetchDelete(
                '/order.php?fn=deleteOrder',
                body,
                userSession.token
            );
        }
        // delete item
        else {
            res = await fetchDelete(
                '/order.php?fn=deleteItemOrder',
                {
                    id: stateModalDelete.id
                },
                userSession.token
            );
        }

        // to change stateModalDelete, re-execute useEffect and refresh users
        const isSelectRow = !stateModalDelete.isDeleteOrder
        if (res !== null) {
            setStateModalDelete({
                isShowModal: false, // hide-modal 
                isDeleteOrder: false,
                isRestore: false,
                subject: '',
                id: -1
            });
            //console.log('idOrderSelected before call loadOrders()', idOrderSelected)
            if (!isSelectRow) loadOrders() // no select row
            else reloadOrders()
        }        
    }

    const onBtnCancelClick = () => {
        setStateModalDelete({
            isShowModal: false,
            isDeleteOrder: false,
            isRestore: false,
            subject: '',
            id: -1
        });
    }

    async function reloadOrders() {
        await loadOrders()
        setIdOrderSelected(idOrderSelected) // reload same id
        let row = document.getElementById(idOrderSelected)
        row.classList.add('active')
        loadItemsOrder(idOrderSelected)

        // get index of order selected
        let i = orders.findIndex(order => order.id === idOrderSelected)
        setIndexOrderSelected(i)
        //console.log('indexOrderSelected: ', i)

        // get index-customer-selected
        i = customers.findIndex(customer => customer.id === orders[i].idCustomer)
        // console.log('indexCustomerSelected: ', i)
        // console.log('customerSelected', customers[i])
        setIndexCustomerSelected(i)
    }

    async function iconMinusClick(id) {
        const i = itemsOrder.findIndex(item => item.id === id)
        if (itemsOrder[i].qty >= 2) {
            const res = await fetchPut('/order.php?fn=updateItemQty', { id: id, qty: -1 }, userSession.token)
            if (res !== null) reloadOrders()            
        }
    }

    async function iconPlusClick(id) {
        const res = await fetchPut('/order.php?fn=updateItemQty', { id: id, qty: 1 }, userSession.token)
        if (res !== null) reloadOrders()       
    }

    function dateChange(e) {
        setDates({
            ...dates, // dame values
            [e.target.name]: e.target.value // only change 1 date named e.target.name
        })
    }

    async function btnDeliverClick(id) {
        const res = await fetchPut(
            `/order.php?fn=deliverOrder&idOrder=${id}`,
            {},
            userSession.token
        )
        if (res !== null) {
            if (idOrderSelected === -1) loadOrders()
            else reloadOrders()
        }        
    }

    /*------------ MODAL ADD ARTICLE ---------------*/

    function btnAddClick() {
        setStateModalAddArticle({
            isShowModal: true,
            idOrder: idOrderSelected
        });
    }

    async function onBtnAcceptAddArticleClick(id) {
        setStateModalAddArticle({
            ...stateModalAddArticle, // same values
            isShowModal: false, idOrder: -1 // only change this property
        });

        // insert item to order
        let res = await fetchPost(
            '/order.php?fn=insertItemOrder',
            {
                idOrder: idOrderSelected,
                idArticle: id,
                qty: 1
            },
            userSession.token
        )
        if (res !== null) reloadOrders()        
    }

    function onBtnCancelAddArticleClick() {
        setStateModalAddArticle({
            ...stateModalAddArticle, // same values
            isShowModal: false, idOrder: -1 // only change this property
        });
    }

    return (
        userSession &&
        <div className='Orders-table-detail'>
            <table className='Orders-table'>
                <caption><h3>Pedidos</h3></caption>
                <thead>
                    <tr>
                        <td colSpan={3}>
                            <label htmlFor="firstDate">Desde </label>
                            <input
                                type="date"
                                name="firstDate"
                                id="firstDate"
                                className='Orders-input Orders-date'
                                value={dates.firstDate}
                                onChange={dateChange}
                            />
                            <label htmlFor="lastDate">  Hasta </label>
                            <input
                                type="date"
                                name="lastDate"
                                id="lastDate"
                                className='Orders-input Orders-date'
                                value={dates.lastDate}
                                onChange={dateChange}
                            />
                        </td>
                        <td colSpan={4}>
                            Cliente&nbsp;
                            <select
                                className='Orders-input'
                                name='select'
                                id='select'
                                onChange={comboCustomersChange}
                            >
                                <option value='-1'>Todos</option>
                                {
                                    customers.map(customer =>
                                        <option key={customer.id} value={customer.id}>{customer.fullname} {'(' + customer.email + ')'}</option>
                                    )
                                }
                            </select>
                        </td>
                    </tr>
                    <tr>
                        <th style={{ width: '10%' }}>Orden</th>
                        <th style={{ width: '20%' }}>Fecha</th>
                        <th style={{ width: '60%' }}>Cliente</th>
                        <th style={{ width: '5%' }}>Arts</th>
                        <th style={{ width: '15%' }}>Total</th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        orders.map((order, i) =>
                            <tr key={order.id} id={order.id} onClick={rowClick}>
                                <td className='Orders-col-number'>{order.id}</td>
                                <td>{order.created}</td>
                                <td>{order.fullname}</td>
                                <td className='Orders-col-number'>{order.items}</td>
                                <td className='Orders-col-number'>${new Intl.NumberFormat('es-MX', { maxFractionDigits: 2 }).format(order.total)}</td>
                                <td>
                                    { // show button deliver if not is delivered
                                        order.isDelivered
                                            ? <code className='Orders-label'>{'ENTREGADO\n' + order.dateDelivered}</code>
                                            : <BtnPrimary text='Entregar' onClick={() => btnDeliverClick(order.id)} />
                                    }
                                </td>
                                <td>
                                    { // isDelivered Delete, else Cancel
                                        order.isDelivered
                                            ? userSession.isAdmin && <BtnDanger text='Eliminar' onClick={() => btnDeleteClick(order.id)} />
                                            : <BtnDanger text='Cancelar' onClick={() => btnDeleteRestoringClick(order.id)} />
                                    }
                                </td>
                            </tr>
                        )
                    }
                </tbody>
                <tfoot>
                    <tr>
                        <td colSpan={4}>{orders.length} {orders.length !== 1 ? 'rdenes' : 'rden'}</td>
                    </tr>
                </tfoot>
            </table>

            {/* ---------------------- TICKET ----------------------- */}
            <article className='Orders-detail Card'>
                <table className='Orders-table-items'>
                    {
                        idOrderSelected > -1 &&
                        <>
                            <caption><b>Pedido: {idOrderSelected}</b></caption>
                            {indexCustomerSelected > -1 &&
                                <>
                                    <caption>{customers[indexCustomerSelected].fullname}</caption>
                                    <caption>{customers[indexCustomerSelected].email}</caption>
                                    <caption>{customers[indexCustomerSelected].phone}</caption>
                                    <caption>{customers[indexCustomerSelected].streetAndNumber}</caption>
                                    <caption>{customers[indexCustomerSelected].suburb}, CP: {customers[indexCustomerSelected].cp}</caption>
                                    <caption>{customers[indexCustomerSelected].town}</caption>
                                    {
                                        !orders[indexOrderSelected].isDelivered &&
                                        <caption style={{ textAlign: 'end' }}><BtnAccept text='Agregar' onClick={btnAddClick} /></caption>
                                    }
                                </>
                            }
                        </>
                    }
                    <thead>
                        <tr>
                            <th style={{ width: '35%' }}>Cant</th>
                            <th style={{ width: '45%' }}>Descripcin</th>
                            <th style={{ width: '10%' }}>Precio</th>
                            <th style={{ width: '10%' }}>Importe</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            itemsOrder.map(item =>
                                <tr key={item.id}>
                                    <td>
                                        { // only show buttons minus-plus if not isDelivered
                                            indexOrderSelected > -1 && orders[indexOrderSelected].isDelivered
                                                ? item.qty
                                                :
                                                <div className='Orders-wrapper-qty'>
                                                    <AiOutlineMinus className='Orders-icon' onClick={() => iconMinusClick(item.id)} />
                                                    {item.qty}
                                                    <AiOutlinePlus className='Orders-icon' onClick={() => iconPlusClick(item.id)} />
                                                </div>
                                        }
                                    </td>
                                    <td className='Orders-col-text'>{item.articleName}</td>
                                    <td className='Orders-col-number'>{item.price}</td>
                                    <td className='Orders-col-number'>{item.amount}</td>
                                    <td>
                                        { // only if items > 1 and !order.isDelivered
                                            itemsOrder.length > 1 && indexOrderSelected > -1 && !orders[indexOrderSelected].isDelivered &&
                                            <AiFillDelete className='Orders-icon-delete' onClick={() => iconDeleteClick(item.id)} />
                                        }
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                    {
                        idOrderSelected > -1 &&
                        <tfoot className='Orders-detail-data'>
                            <tr>
                                <td colSpan={2}>{itemsOrder.length} artculos</td>
                                <td className='Orders-col-number'>Total</td>
                                <td className='Orders-col-number'>${new Intl.NumberFormat('es-MX', { maximumFractionDigits: 2 }).format(orders.find(order => order.id === idOrderSelected).total)}</td>
                            </tr>
                        </tfoot>
                    }

                </table>
            </article>
            <ModalDelete
                isShowModal={stateModalDelete.isShowModal}
                subject={stateModalDelete.subject}
                btnDeleteClick={onBtnDeleteClick}
                btnCancelClick={onBtnCancelClick}
            />
            <ModalAddArticle
                isShowModal={stateModalAddArticle.isShowModal}
                idOrder={stateModalAddArticle.idOrder}
                btnAcceptClick={onBtnAcceptAddArticleClick}
                btnCancelClick={onBtnCancelAddArticleClick}
            />
        </div>
    )
}

export default OrdersList