import React, { useContext, useState, useEffect, useCallback } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { AiFillCheckCircle } from 'react-icons/ai';
// context
import { DataContext } from '../../context/DataContext';
// components
import BtnAccept from '../buttons/BtnAccept';
import BtnPrimary from '../buttons/BtnPrimary';
import BtnDanger from '../buttons/BtnDanger';
import ModalDelete from '../modal_delete/ModalDelete';
// css
import './ArticlesList.css';


const Articles = () => {
    //console.log('>>> Articles load...');

    // context
    const { fetchGet, fetchDelete, userSession } = useContext(DataContext);

    // states
    const [articles, setArticles] = useState([]);
    const [articleSelected, setArticleSelected] = useState(null);
    const [stateModalDelete, setStateModalDelete] = useState({
        isShowModal: false,
        id: -1
    });
    const [txtSearch, setTxtSearch] = useState('')

    const navigate = useNavigate();

    // protected of re-render with useCallback()
    const articlesLoad = useCallback(async () => {
        // load categories
        const json = await fetchGet(
            '/article.php?fn=getAllArticles&txtSearch=' + txtSearch,
            userSession['token']
        );
        //console.log(json);

        if (json !== null) setArticles(json['articles']); 
               
    }, [fetchGet, txtSearch, userSession]);

    useEffect(() => {
        //console.log('useEffect Articles');

        if (userSession) articlesLoad();
        else navigate('/not-found');

        return () => {}

    }, [articlesLoad, navigate, userSession, stateModalDelete]);

    const searchChange = (e) => {
        //console.log(e.target.value);
        setTxtSearch(e.target.value);
        //console.log(txtSearch);
    }

    const searchKeyPress = (e) => {
        //console.log(e.key);

        if (e.key === 'Enter')
            articlesLoad();
    }

    const btnSeeClick = async (id) => {
        //console.log(id);
        // load article

        const json = await fetchGet(
            '/article.php?fn=getArticle&id=' + id,
            userSession['token']
        );
        //console.log(json);

        if (json !== null) setArticleSelected(json['article']);        
    }

    const btnDeleteClick = (id) => {
        //console.log('id before delete: ', id);

        setStateModalDelete({
            isShowModal: true, // show-Modal
            id: id // only change id
        });
        //console.log('after: ', stateModalDelete);
    }

    const onDelete = async () => {
        //console.log('to delete: ', stateModalDelete['id']);


        const json = await fetchDelete(
            '/article.php?fn=deleteArticle',
            {
                id: stateModalDelete['id']
            },
            userSession['token']
        );
        //console.log(json);

        // to change stateModalDelete, re-execute useEffect and refresh users
        if (json !== null) {
            setArticleSelected(null);
            setStateModalDelete({
                isShowModal: false, // hide-modal 
                id: -1
            });
        }
    }

    const btnCancelClick = () => {
        setStateModalDelete({
            isShowModal: false,
            id: -1
        });
    }

    const rowClick = (e) => {
        //console.log(e.target.parentElement.id);

        // only click over <td>
        if (e.target.tagName === 'TD') {
            // clean selection
            let row = document.getElementsByClassName('active')[0];
            //console.log(rowSelected);
            if (row) row.classList.remove('active');
            const id = e.target.parentElement.id;
            row = document.getElementById(id);
            row.classList.add('active');
            btnSeeClick(id)
        }
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <table className="Table">
                <caption><h2>Artculos</h2></caption>
                <thead>
                    <tr>
                        <td colSpan={3}><input type="search" onChange={searchChange} onKeyPress={searchKeyPress} placeholder='Buscar...' autoFocus /></td>
                        <td colSpan={2}>
                            <Link to='/article-insert'>
                                <BtnAccept text='Nuevo' />
                            </Link>
                        </td>
                    </tr>
                    <tr>
                        <th>Titulo</th>
                        <th>Categora</th>
                        <th>Precio</th>
                        <th>Disponible</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        articles.map(article =>
                            <tr key={article.id} id={article.id} onClick={rowClick}>
                                <td>{article.articleName}</td>
                                <td>{article.categoryName}</td>
                                <td className='Table-cell-end'>$ {article.price}</td>
                                <td className="Table-cell-center">{article.isAvailable && <AiFillCheckCircle className="Table-icon" />}</td>
                                <td>
                                    { // only if is admin
                                        userSession.isAdmin &&
                                        <BtnDanger text='Eliminar' onClick={() => btnDeleteClick(article.id)} />
                                    }
                                </td>
                            </tr>
                        )
                    }
                </tbody>
                <tfoot>
                    <tr>
                        {<td className='Table-cell-center' colSpan={4}>{articles.length} artculos</td>}
                    </tr>
                </tfoot>
            </table>
            <div className='Card'>
                <div className='Card-article-border' >

                    { // show only if userSelected not is null
                        articleSelected != null &&
                        <div className='Card-article-container' >
                            <div className='Card-article-data'>
                                <img src={articleSelected['imgPath']} alt="" width="100" />
                                <h5>Categora</h5>
                                <pre>{articleSelected['categoryName']}</pre>
                                <h5>Titulo</h5>
                                <pre>{articleSelected['articleName']}</pre>
                                <h5>Descripcin</h5>
                                <pre>{articleSelected['description']}</pre>
                                <h5>Precio</h5>
                                <pre>$ {articleSelected['price']}</pre>
                                <h5>Existencia</h5>
                                <pre>{articleSelected['stock']}</pre>
                            </div>
                            { // only if is admin
                                userSession.isAdmin &&
                                <BtnPrimary
                                    text='Modificar'
                                    style={{ display: 'block' }}
                                    onClick={() => navigate(`/article-update/${articleSelected['id']}`)}
                                />
                            }
                        </div>
                    }
                </div>
            </div>
            <ModalDelete
                isShowModal={stateModalDelete['isShowModal']}
                subject={'Ests segur@ de eliminar el artculo?'}
                btnDeleteClick={onDelete}
                btnCancelClick={btnCancelClick}
            />
        </div>
    )
}

export default Articles