// React
import { useContext, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AiFillCheckCircle } from 'react-icons/ai';
// context
import { DataContext } from "../../context/DataContext";
// components
import BtnAccept from "../buttons/BtnAccept";
import BtnPrimary from "../buttons/BtnPrimary";
import BtnDanger from "../buttons/BtnDanger";
import ModalDelete from "../modal_delete/ModalDelete";
import ModalInformation from "../modal_information/ModalInformation";

export default function UsersList() {
    // context
    const { fetchGet, fetchDelete, userSession } = useContext(DataContext);
    // states    
    const [users, setUsers] = useState([]);
    const [stateModalDelete, setStateModalDelete] = useState({
        isShowModal: false,
        id: -1
    });
    const [isShowModalInfo, setIsShowModalInfo] = useState(false);

    const navigate = useNavigate();

    useEffect(() => {
        //console.log('useEffect UsersList');

        async function loadUsers() {
            let data = await fetchGet('/user.php?fn=getAllUsers', userSession['token']);
            //console.log(data);

            if (data !== null) setUsers(data['users']);
        }

        if (userSession && userSession['id'] === 0) loadUsers();
        else navigate('/not-found')
    }, [fetchGet, userSession, stateModalDelete, navigate]); // when change on modalDelete for refresh users

    const btnDeleteClick = (id) => {
        //console.log('before: ', stateModalDelete);

        if (id !== 0) {
            setStateModalDelete({
                isShowModal: true, // show-Modal
                id: id // only change id
            });
            //console.log('after: ', stateModalDelete);
        }
        else setIsShowModalInfo(true);
    }

    const onDelete = async () => {
        //console.log('to delete: ', stateModalDelete['id']);

        const json = await fetchDelete(
            '/user.php?fn=deleteUser',
            {
                id: stateModalDelete['id']
            },
            userSession['token']
        );
        //console.log(json);

        // to change stateModalDelete, re-execute useEffect and refresh users
        if (json !== null)
            setStateModalDelete({
                isShowModal: false, // hide-modal 
                id: -1
            });
    }

    const btnCancelClick = () => {
        setStateModalDelete({
            isShowModal: false,
            id: -1
        });
    }

    const btnCloseClick = () => {
        setIsShowModalInfo(false);
    }

    return (
        userSession &&
        <div className="Wrapper-flex">
            <table className="Table">
                <caption><h2>Lista de usuarios</h2></caption>
                <thead>
                    <tr>
                        <td colSpan={4}></td>
                        <td>
                            <Link to="/user-insert" className="Table-link-add" style={{ textDecoration: 'none' }}>
                                <BtnAccept text='Nuevo' />
                            </Link>
                        </td>
                    </tr>
                    <tr>
                        <th>Usuario</th>
                        <th>Correo</th>
                        <th>Administrador</th>
                        <th>Activo</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        users.map(user =>
                            <tr key={user.id}>
                                <td>{user.userName}</td>
                                <td>{user.email}</td>
                                <td className="Table-cell-center">{user.isAdmin && <AiFillCheckCircle className="Table-icon" />}</td>
                                <td className="Table-cell-center">{user.isActive && <AiFillCheckCircle className="Table-icon" />}</td>
                                <td>
                                    <BtnPrimary text='Modificar' onClick={() => navigate(`/user-update/${user.id}`)} />
                                    <BtnDanger text='Eliminar' onClick={() => btnDeleteClick(user.id)} />
                                </td>
                            </tr>
                        )
                    }
                </tbody>
                <tfoot>
                    <tr>
                        <td colSpan={5}>{users.length} usuarios</td>
                    </tr>
                </tfoot>
            </table>
            <ModalDelete
                isShowModal={stateModalDelete['isShowModal']}
                subject='Ests segur@ de eliminar el usuario?'
                btnDeleteClick={onDelete}
                btnCancelClick={btnCancelClick}
            />
            <ModalInformation
                isShowModal={isShowModalInfo}
                subject='El usuario no puede ser eliminado porque es el super-admin'
                btnCloseClick={btnCloseClick}
            />
        </div>
    );
}