import React, { useContext, useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router'
import { Link } from 'react-router-dom'
// context
import { DataContext } from '../../context/DataContext'
// components
import FormMessage from '../form/FormMessage'

const CustomerActivation = () => {
  // context
  const { customerSession, fetchGet, fetchPut } = useContext(DataContext)
  // params
  const params = useParams()
  // states
  const [idCustomer, setIdCustomer] = useState(-1)
  const [msg, setMsg] = useState('')
  const [isSuccess, setIsSuccess] = useState(false)
  // navigate
  const navigate = useNavigate()

  useEffect(() => {
    //console.log(params)
    window.history.replaceState(null, '', '/customer-activation') // clean token

    async function getIdCustomerFromToken() {
      // get idCustomer from token (-1 not exists); if exists change token
      let res = await fetchGet(`/index.php?fn=getIdCustomerFromToken&token=${params['token']}`)
      if (res !== null) {
        //console.log(res)
        setIdCustomer(res['idCustomer'])

        // verify if idCustomer > -1
        if (res['idCustomer'] > -1) {
          // activate account of customer
          res = await fetchPut(
            `/index.php?fn=activateCustomer`,
            {
              idCustomer: res['idCustomer']
            }
          )
          setIsSuccess(true)
          setMsg('Tu cuenta ha sido activada.')
        }
        else {
          setIsSuccess(false)
          setMsg('El link de activacin no es vlido.')
        }
      }
    }

    if (!customerSession && params['token']) getIdCustomerFromToken()
    else navigate('/not-found')
  }, [customerSession, params, navigate, fetchGet, fetchPut])

  return (
    !customerSession &&
    <>
      <p className='ProductsTitle'>ACTIVACIN DE CUENTA</p>
      <div className='Wrapper-flex'>
        <div className='Card' style={{ width: '300px' }}>
          <FormMessage msg={msg} isSuccess={isSuccess} />
          {
            idCustomer > -1 &&              
              <Link to='/input'>
                <p>Iniciar sesin</p>
              </Link>
          }
        </div>
      </div>
    </>
  )
}

export default CustomerActivation