import { useNavigate } from 'react-router-dom';
import { useState, useEffect, useContext } from "react";
// context
import { DataContext } from '../../context/DataContext';
// components
import FormMessage from "../form/FormMessage";

export default function UserInput() {
    // context
    const { fetchPostInput, userSession, setUserSession } = useContext(DataContext);

    // states
    const [inputsTxt, setInputsTxt] = useState({
        email: '',
        pass: '',
    });
    const [msg, setMsg] = useState('');

    const navigate = useNavigate();

    // only first load
    useEffect(() => {
        //console.log('useEffect UserInput');
        if (userSession) navigate('/');
    }, [userSession, navigate]);

    const showFormMessage = (msg) => {
        setMsg(msg);
    }

    const formOnSubmit = async (e) => {
        e.preventDefault();

        let res = await fetchPostInput('/user.php?fn=getDataUserFromPass', inputsTxt); // with token = TOKEN_DEFAULT
        setInputsTxt({
            email: '',
            pass: ''
        })        
        
        if (res.isAuth) {
            res = res['user']
            // console.log(json['isActive']);
            // return;

            if (res['isActive']) {
                // clear and save user on sessionStorage
                window.sessionStorage.clear()
                window.sessionStorage.setItem('damax', JSON.stringify(res));

                // update user-on-session-state
                setUserSession(res);

                // redirect to root
                //navigate('/'); // import useNavigate from react-router-dom
            }
            else showFormMessage('El usuario no est activo')
        }
        else showFormMessage('Correo y/o contrasea incorrecta');        
    }

    const txtOnChange = (e) => {
        //console.log(e.target.value);        
        setInputsTxt({
            ...inputsTxt,
            [e.target.name]: e.target.value // same name inputs and variables
        });
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        !userSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>INGRESO DE USUARIO AUTORIZADO</h3>
                <form onSubmit={formOnSubmit}>
                    <input
                        type="email"
                        name="email"
                        placeholder='Correo'
                        value={inputsTxt.email}
                        onChange={txtOnChange}
                        required
                    />
                    <input
                        type="password"
                        name="pass"
                        placeholder='Contrasea'
                        value={inputsTxt.pass}
                        onChange={txtOnChange}
                        required
                    />
                    {/* <Link to="/">
                <span>Olvid mi contrasea</span>
            </Link> */}
                    <input type="submit" value="INICIAR SESIN" />
                </form>
                {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />}
            </div>
        </div>
    );
}