import React, { useState, useContext, useEffect, useCallback } from 'react'
import { useNavigate, useParams } from 'react-router'
// context
import { DataContext } from '../../context/DataContext';
// components
import BtnPrimary from '../buttons/BtnPrimary';
import BtnSecundary from '../buttons/BtnSecundary';
import FormMessage from '../form/FormMessage';

const ArticleForm = () => {
    // context
    const { fetchGet, fetchPost, fetchPut, userSession } = useContext(DataContext)

    // param
    const params = useParams();
    const id = params['id'] ?? -1;

    // states
    const [categories, setCategories] = useState([]);
    const [article, setArticle] = useState({
        id: params['id'] ?? -1,
        imgPath: 'http://192.168.0.27/react_php-catalog/backend/img/noImage.png',
        imgBase64: '', // base64, if not image = '' !important 
        idCategory: -1,
        articleName: '',
        description: '',
        price: '',
        stock: '',
        isAvailable: true
    });
    const [msg, setMsg] = useState('');

    const navigate = useNavigate();

    const inputChange = (e) => {
        //console.log(e.target.value);
        const { name, value } = e.target;
        //console.log(name, value);
        //console.log(article);
        setArticle({
            ...article, // same data
            [name]: name !== 'isAvailable' ? value : e.target.checked // only change 1 field
        })
    }

    const fileChange = (e) => {
        const file = e.target.files[0];
        if (FileReader && file) {
            const fr = new FileReader();
            fr.onload = () => {
                document.getElementById('imgArticle').src = fr.result; // fr.result = base64
                //console.log(fr.result);
                setArticle({
                    ...article,
                    imgBase64: fr.result
                });
            }
            fr.readAsDataURL(file);
        }
    }

    const formSubmit = async (e) => {
        e.preventDefault();
        //console.log('article before save: ', article);

        let json = await fetchGet(`/article.php?fn=isExistsArticle&articleName=${article['articleName'].trim()}&id=${article['id']}`, userSession['token']);
        //console.log(json);

        if (json !== null) {
            if (!json['isExistsArticle']) {

                // insert article
                if (article.id === -1) 
                    json = await fetchPost('/article.php?fn=insertArticle', article, userSession['token']);
                
                // update article
                else
                    json = await fetchPut('/article.php?fn=updateArticle', article, userSession['token']);

                //console.log(json);

                navigate('/articles');                
            }
            // show message
            setMsg('El artculo ya existe');
        }        
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    const categoriesLoad = useCallback(async () => {
        // if id > -1 load article

        let json = await fetchGet('/category.php?fn=getAllCategories', userSession['token']);
        //console.log(json);

        //json['isAuth'] = false; // test if is not authorized
        if (json !== null) {
            json = json['categories'];
            //console.log(json);
            setCategories(json);

            //------- load article -------//
            if (article.id > -1) {
                json = await fetchGet('/article.php?fn=getArticle&id=' + article.id, userSession['token']);
                //console.log(json);

                if (json !== null) {
                    json = json['article'];
                    json['imgBase64'] = '';
                    //console.log(json);
                    setArticle(json);
                }               
            }            
        }        
    }, [fetchGet, userSession, article.id])

    useEffect(() => {
        //console.log('>>> useEffect ArticleForm');

        if (userSession) categoriesLoad();
        else navigate('/not-found');

        return () => {}
    }, [userSession, categoriesLoad, navigate]);

    return (
        userSession &&
        <div className='Wrapper-flex'>
            {/* {console.log(article)} */}
            <div className="Card">
                <h4>{id === -1 ? 'Agregando' : 'Modificando'} artculo</h4>
                <div className='Card-img-container'>
                    <img src={article.imgPath} id='imgArticle' alt='Artculo' style={{ width: '100%', height: '100%' }} />
                </div>
                <form className='Form' onSubmit={formSubmit}>
                    <input
                        type="file"
                        name="img"
                        id="img"
                        accept='image/*'
                        onChange={fileChange}
                    />
                    <label htmlFor="idCategory">Categora</label>
                    <select
                        name="idCategory"
                        id="idCategory"
                        value={article.idCategory}
                        onChange={inputChange}
                        required
                    >
                        { // load categories on select or combobox
                            categories.map(category =>
                                <option
                                    key={category.id}
                                    value={category.id}
                                // selected={category == article.idCategory}
                                >{category.categoryName}</option>
                            )
                        }
                    </select>
                    <label htmlFor="articleName">Titulo</label>
                    <input
                        type="text"
                        id="articleName"
                        name="articleName"
                        value={article.articleName}
                        onChange={inputChange}
                        autoFocus
                        required
                    />
                    <label htmlFor="description">Descripcin</label>
                    <textarea
                        name="description"
                        id="description"
                        value={article.description}
                        onChange={inputChange}
                    ></textarea>
                    <div className='Form-number-pair'>
                        <label htmlFor="price">Precio</label>
                        <input
                            type="number"
                            name="price"
                            id="price"
                            value={article.price}
                            onChange={inputChange}
                            required
                        />
                        <label htmlFor="stock" style={{ marginLeft: '20px' }}>Existencia</label>
                        <input
                            type="number"
                            name="stock"
                            id="stock"
                            value={article.stock}
                            onChange={inputChange}
                            required
                        />
                    </div>
                    <label htmlFor="isAvailable">Disponible</label>
                    <input
                        type="checkbox"
                        id='isAvailable'
                        name="isAvailable"
                        checked={article.isAvailable}
                        onChange={inputChange}
                    />
                    <div className='Form-user-actions'>
                        <BtnPrimary text='Guardar' type='submit' />
                        <BtnSecundary text='Cancelar' onClick={() => navigate('/articles')} />
                    </div>

                    { // show only if msg !== ''
                        msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                    }

                </form>
            </div>
        </div>
    )
}

export default ArticleForm