import React, { useContext, useState } from 'react'

// components
import FormMessage from '../form/FormMessage'

// context
import { DataContext } from '../../context/DataContext'

const FormCustomerPass = () => {
  // context
  const { fetchPost, customerSession } = useContext(DataContext)

  const inputsDefaultValues = {
    current: '',
    pass: '',
    confirm: ''
  }

  // states
  const [inputs, setInputs] = useState(inputsDefaultValues)
  const [msg, setMsg] = useState('')
  const [isSuccess, setIsSuccess] = useState(false)

  function inputChange(e) {
    setInputs({
      ...inputs, // same values
      [e.target.name]: e.target.value // only change property same name
    })
  }

  async function formSubmit(e) {
    e.preventDefault()

    // verify current pass
    let res = await fetchPost('/customer_authorized.php?fn=isSamePass', { pass: inputs.current }, customerSession.token)
    if (res !== null) {
      if (res['isSamePass']) {
        if (inputs.pass === inputs.confirm) {
          await fetchPost('/customer_authorized.php?fn=changePass', { pass: inputs.pass }, customerSession.token)
          setIsSuccess(true)
          setMsg('La contrasea ha sido actualizada')
        }
        else setMsg('Nueva contrasea y confirmacin son diferentes')
      }
      else setMsg('Contrasea actual incorrecta')
      setInputs(inputsDefaultValues)
    }    
  }

  return (
    <form className='FormCard' onSubmit={formSubmit}>
      <input
        type="password"
        name="current"
        value={inputs.current}
        placeholder='Contrasea actual'
        onChange={inputChange}
        required
      />
      <input
        type="password"
        name="pass"
        value={inputs.pass}
        placeholder='Nueva contrasea'
        onChange={inputChange}
        required
      />
      <input
        type="password"
        name="confirm"
        value={inputs.confirm}
        placeholder='Confirmar nueva contrasea'
        onChange={inputChange}
        required
      />
      {
        !isSuccess &&
        <input type="submit" value="Aceptar" />
      }
      {
        msg.length !== 0 &&
        <FormMessage msg={msg} btnCloseClick={() => setMsg('')} isSuccess={isSuccess} />
      }
    </form>
  )
}

export default FormCustomerPass