import React, { useState, useContext, useEffect, useCallback } from 'react'
import { useNavigate, useParams } from 'react-router'
// context
import { DataContext } from '../../context/DataContext';
// components
import BtnPrimary from '../buttons/BtnPrimary';
import BtnSecundary from '../buttons/BtnSecundary';
import FormMessage from '../form/FormMessage';
// css
import './CustomerForm.css';

const CustomerForm = () => {
    // context
    const { fetchGet, fetchPost, fetchPut, userSession } = useContext(DataContext)

    // param
    const params = useParams();
    const id = params['id'];

    // states    
    const [customer, setCustomer] = useState({
        id: params['id'] ?? -1,
        email: '',
        fullname: '',
        phone: '',
        streetAndNumber: '',
        suburb: '',
        cp: '',
        town: '',
        isActive: true,
        pass: '',
        confirm: ''
    });
    const [isShowPass, setIsShowPass] = useState(false)
    const [msg, setMsg] = useState('');
    
    // navigate
    const navigate = useNavigate();

    const inputChange = (e) => {
        //console.log(e.target.value);
        const { name, value } = e.target;        

        // if (name !== 'isChangePass') {
        setCustomer({
            ...customer, // same data
            [name]: name !== 'isActive' ? value : e.target.checked // only change 1 field
        })
        // }
        // else setIsShowPass(e.target.checked)
    }

    const formSubmit = async (e) => {
        e.preventDefault();
        //console.log('customer before save: ', customer);

        let json = await fetchGet(`/customer.php?fn=isExistsCustomer&email=${customer['email'].trim()}&id=${customer['id']}`, userSession['token']);
        // console.log(json);
        // return;

        if (json !== null) {
            if (!json['isExistsCustomer']) {
                // console.log(customer.id);
                // return;

                // verify if showPass and pass-confirm equals
                let isContinue = false
                if (isShowPass) {
                    if (customer.pass === customer.confirm) isContinue = true
                    else {
                        setCustomer({
                            ...customer,
                            pass: '',
                            confirm: ''
                        });
                        setMsg('Contrasea y confirmacin son diferentes')
                    }
                }
                else isContinue = true

                if (isContinue) {
                    // insert customer
                    if (customer.id === -1) {
                        json = await fetchPost('/customer.php?fn=insertCustomer&isAutoRegister=false', customer, userSession['token']);
                    }

                    // update customer
                    else
                        json = await fetchPut('/customer.php?fn=updateCustomer', customer, userSession['token']);

                    // console.log(json);
                    // return;

                    navigate('/customers');                    
                }
            }
            else setMsg('El cliente ya existe');
        }
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    const customerLoad = useCallback(async () => {
        if (customer['id'] > -1) {
            let res = await fetchGet('/customer.php?fn=getCustomer&id=' + customer.id, userSession['token']);
            //console.log(json);

            if (res !== null) {
                res = res['customer'];

                // add pass empty
                res['pass'] = ''
                res['confirm'] = ''
                //console.log(json);
                setCustomer(res);
            }
        }
    }, [fetchGet, userSession, customer])

    useEffect(() => {
        //console.log('>>> useEffect CustomerForm');

        if (userSession) {
            if (id) customerLoad()
            else setIsShowPass(true) // pass obligatory
        }
        else navigate('/not-found')

        return () => {}
    }, [customerLoad, id, userSession, navigate]);

    const toggleShowPass = (e) => {
        //console.log(e.target.checked);
        setIsShowPass(e.target.checked);
    }

    return (    
        userSession &&    
        <div className='Wrapper-flex'>
            <div className="Card">
                <h4>{id === undefined ? 'Agregando' : 'Modificando'} cliente</h4>
                <form className='Form' onSubmit={formSubmit}>
                    <label htmlFor="email">Correo</label>
                    <input
                        type="text"
                        id="email"
                        name="email"
                        value={customer.email}
                        onChange={inputChange}
                        autoFocus
                        required
                    />
                    <label htmlFor="fullname">Nombre</label>
                    <input
                        type="text"
                        id="fullname"
                        name="fullname"
                        value={customer.fullname}
                        onChange={inputChange}
                        required
                    />
                    <label htmlFor="phone">Telfono</label>
                    <input
                        type='text'
                        id="phone"
                        name="phone"
                        value={customer.phone}
                        onChange={inputChange}
                    />
                    <label htmlFor="streetAndNumber">Calle y nmero</label>
                    <input
                        type='text'
                        id="streetAndNumber"
                        name="streetAndNumber"
                        value={customer.streetAndNumber}
                        onChange={inputChange}
                    />
                    <div className='Form-input-pair-inline'>
                        <div>
                            <label htmlFor="suburb">Colonia</label>
                            <input
                                type='text'
                                id="suburb"
                                name="suburb"
                                value={customer.suburb}
                                onChange={inputChange}
                            />
                        </div>
                        <div>
                            <label htmlFor="cp">C.P.</label>
                            <input
                                type='text'
                                id="cp"
                                name="cp"
                                value={customer.cp}
                                onChange={inputChange}
                            />
                        </div>
                    </div>
                    <label htmlFor="town">Municipio</label>
                    <input
                        type='text'
                        id="town"
                        name="town"
                        value={customer.town}
                        onChange={inputChange}
                    />
                    <label htmlFor="isActive">Activo</label>
                    <input
                        type="checkbox"
                        id='isActive'
                        name="isActive"
                        checked={customer.isActive}
                        onChange={inputChange}
                    />

                    {  // show only if editing
                        id &&
                        <div className='Form-customer-check-pass'>
                            <label htmlFor="password">Cambiar contrasea</label>
                            <input
                                type="checkbox"
                                id="isShowPass"
                                onChange={toggleShowPass}
                            />
                        </div>
                    }
                    { // show only if above checkbox is checked
                        isShowPass &&
                        <div className='Form-customer-pair-pass'>
                            <div>
                                <label htmlFor="password">Contrasea</label>
                                <input
                                    type="password"
                                    id='pass'
                                    name="pass"
                                    value={customer.pass}
                                    onChange={inputChange}
                                    required
                                //required={id || isShowPass /* with id or user-editing with checked-showPass*/}
                                />
                            </div>
                            <div>
                                <label htmlFor="confirm">Confirmar</label>
                                <input
                                    type="password"
                                    id='confirm'
                                    name="confirm"
                                    value={customer.confirm}
                                    onChange={inputChange}
                                    required
                                //required={id || isShowPass /* with id or user-editing with checked-showPass */}
                                />
                            </div>
                        </div>
                    }

                    <div className='Form-customer-actions'>
                        <BtnPrimary text='Guardar' type='submit' />
                        <BtnSecundary text='Cancelar' onClick={() => navigate('/customers')} />
                    </div>

                    { // show only if msg !== ''
                        msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                    }

                </form>
            </div>
        </div>
    )
}

export default CustomerForm