import React from 'react'
import FormCustomerData from '../form/FormCustomerData'

const CustomerRegister = () => {
  return (
    <>
      <p className='ProductsTitle'>REGISTRO DE CLIENTE</p>
      <div className='Wrapper-flex'>
        <FormCustomerData isAutoRegister={true} />
      </div>
    </>
  )
}

export default CustomerRegister