// React
import { AiOutlineCloseCircle } from 'react-icons/ai';
// css
import './FormMessage.css';

export default function FormMessage({msg, btnCloseClick, isSuccess = false}) {
    
    return (
        <div className={isSuccess ? 'FormMessageWrapper success' : 'FormMessageWrapper'} id='formMessageWrapper'>
            <AiOutlineCloseCircle className='FormMessageIcon' onClick={btnCloseClick} />
            <pre>{msg}</pre> {/* pre-formatter with line breaks */}
        </div>
    );
}