import React, { useEffect, useState, useContext } from 'react'
import { useParams, useNavigate } from 'react-router-dom'
// context
import { DataContext } from '../../context/DataContext';
// icon
import { AiOutlineArrowLeft, AiOutlinePlus, AiOutlineMinus } from 'react-icons/ai';
// components
import FormMessage from '../form/FormMessage';
// css
import './ProductDetail.css';

const ProductDetail = () => {
  // context 
  const { fetchGet, userSession, articlesCart, setArticlesCart } = useContext(DataContext);
  // params
  const params = useParams();
  // navigate
  const navigate = useNavigate();
  // states
  const [article, setArticle] = useState({});
  const [qty, setQty] = useState(1);
  const [msg, setMsg] = useState('');

  useEffect(() => {
    //console.log('useEffect ProductDetail');

    async function getArticle() {
      const data = await fetchGet('/index.php?fn=getArticle&id=' + params['id']);
      if (data !== null) setArticle(data['article']);      
    }
    getArticle();

    return () => {
      //console.log('useEffect ProductDetail bye')
    }
  }, [params, fetchGet]);

  useEffect(() => {
    // save to storage
    window.localStorage.setItem('cart', JSON.stringify(articlesCart))
  
    return () => {
      //console.log('useEffect articlesCart-change bye')
    }
  }, [articlesCart])
  

  const backClick = () => {
    // const path 
    //navigate('/productos/' + article['categoryName']);
    navigate(-1)
  }

  const addClick = () => {
    setQty(qty + 1);
  }

  const substractClick = () => {
    if (qty >= 2) setQty(qty - 1);
  }

  const btnAddToCartClick = () => {
    //console.log('btnAddToCart: id=' + article.id + ', qty=' + qty);
    article['qty'] = qty;
    //console.log('articlesCart before change: ', articlesCart);

    // update article if exists or add if not exists

    // take index of articles with id equals
    const i = articlesCart.findIndex(articleCart => articleCart.id === article.id)
    //console.log('articleIndex: ', articleIndex)
    
    // add new article to cart
    if (i === -1) {
      setArticlesCart([
        ...articlesCart, // same list
        article // + new article
      ])      
    }
    // only add qty to article exists
    else {
      // change qty of article exists: qty + articlesExists[0].qty
      let articleExists = articlesCart[i]
      //console.log('articleExists', articleExists)
      articleExists['qty'] += qty;
      //console.log('articleExists update qty: ', articleExists)      
    }
    setMsg('El artculo ha sido agregado al carrito');    
  }

  const onCloseFormMessage = () => {
    setMsg('');
  }

  return (
    <>
      {/* ------------------- BACK BAR --------------------- */}
      <div className='Detail-top'>
        <AiOutlineArrowLeft className='Detail-icon' onClick={backClick} />
      </div>

      <div className='Wrapper-flex'>
        
        {/* ------------------- CARD IMAGE --------------------- */}
        <div className='Card Detail-card'>
          <div className='Detail-container Detail-zoom'>
            <img src={article['imgPath']} alt="" />
          </div>
        </div>

        {/* -------------- DATA CARD -------------- */}
        <div className='Card Detail-card'>
          <div className='Detail-container Detail-data'>
            <h4>DAMAX BOUTIQUE</h4>
            <p className='Detail-title'>{article['articleName']}</p>
            <p className='Detail-price'>$ {article['price']}</p>

            {/* qty */}
            <div className='Detail-qty'>
              <AiOutlineMinus className='Detail-icon' onClick={substractClick} />
              <span>{qty}</span>
              <AiOutlinePlus className='Detail-icon' onClick={addClick} />
            </div>

            <div className='Detail-stock'>
              <span>{article['stock'] > 0 ? 'Solo restan: ' + article['stock'] : 'AGOTADO'}</span>
            </div>
            <p className='Detail-title'>Descripcin</p>
            <pre className='Detail-description'>{article['description']}</pre>

            { // only if not user on session and stock >= qty
              !userSession && article['stock'] >= qty &&
              <button className='Detail-btn-cart' onClick={btnAddToCartClick}>AGREGAR AL CARRITO</button>
            }

            {msg !== '' && <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} isSuccess={true} />}

          </div>
        </div>
      </div>
    </>
  )
}

export default ProductDetail