import React from 'react'
import { Link } from 'react-router-dom'

const NotFound = () => {
  window.history.replaceState(null, '', '/not-found')
  return (
    <div 
    className='Wrapper-flex' 
    style={{
        flexDirection: 'column',
        alignItems: 'center'
    }}
    >
        <h3>Pgina no encontrada</h3>
        <code>(Error 404)</code>
        <Link to='/'>
            <p>INICIO</p>
        </Link>
    </div>
  )
}

export default NotFound