import React, { useContext, useState, useEffect, useCallback } from 'react'
import { Link, useNavigate } from 'react-router-dom';
import { AiFillCheckCircle } from 'react-icons/ai';
// context
import { DataContext } from '../../context/DataContext';
// components
import BtnAccept from '../buttons/BtnAccept';
import BtnPrimary from '../buttons/BtnPrimary';
import BtnDanger from '../buttons/BtnDanger';
import ModalDelete from '../modal_delete/ModalDelete';
// css
import './CustomersList.css';


const Customers = () => {
    // context
    const { fetchGet, fetchDelete, userSession } = useContext(DataContext);

    // states
    const [customers, setCustomers] = useState([]);
    const [customerSelected, setCustomerSelected] = useState(null);
    const [stateModalDelete, setStateModalDelete] = useState({
        isShowModal: false,
        id: -1
    });
    const [txtSearch, setTxtSearch] = useState('')

    const navigate = useNavigate();

    // protected of re-render with useCallback()
    const customersLoad = useCallback(async () => {
        // load customers
        const json = await fetchGet(
            '/customer.php?fn=getAllCustomers&txtSearch=' + txtSearch,
            userSession['token']
        );
        //console.log(json);
        
        if (json !== null) setCustomers(json['customers']);
        
    }, [fetchGet, txtSearch, userSession]);

    useEffect(() => {
        //console.log('useEffect Customers');

        if (userSession) customersLoad();
        else navigate('/not-found');

        return () => {}

    }, [customersLoad, navigate, userSession, stateModalDelete]);

    const searchChange = (e) => {
        //console.log(e.target.value);
        setTxtSearch(e.target.value);
        //console.log(txtSearch);
    }

    const searchKeyPress = (e) => {
        //console.log(e.key);

        if (e.key === 'Enter')
            customersLoad();
    }

    const btnSeeClick = async (id) => {
        //console.log(id);
        // load article

        const json = await fetchGet(
            '/customer.php?fn=getCustomer&id=' + id,
            userSession['token']
        );
        //console.log(json);

        if (json !== null) setCustomerSelected(json['customer'])            
    }

    const btnDeleteClick = (id) => {
        //console.log('id before delete: ', id);

        setStateModalDelete({
            isShowModal: true, // show-Modal
            id: id // only change id
        });
        //console.log('after: ', stateModalDelete);
    }

    const onDelete = async () => {
        //console.log('to delete: ', stateModalDelete['id']);


        const json = await fetchDelete(
            '/customer.php?fn=deleteCustomer',
            {
                id: stateModalDelete['id']
            },
            userSession['token']
        );
        //console.log(json);

        // to change stateModalDelete, re-execute useEffect and refresh users
        if (json !== null) {
            setCustomerSelected(null);
            setStateModalDelete({
                isShowModal: false, // hide-modal 
                id: -1
            });
        }
    }

    const btnCancelClick = () => {
        setStateModalDelete({
            isShowModal: false,
            id: -1
        });
    }

    const rowClick = (e) => {
        //console.log(e.target.parentElement.id);
        
        // only click over <td>
        if (e.target.tagName === 'TD') {
            // clean selection
            let row = document.getElementsByClassName('active')[0];
            //console.log(rowSelected);
            if (row) row.classList.remove('active');
            const id = e.target.parentElement.id;
            row = document.getElementById(id);
            row.classList.add('active');
            btnSeeClick(id)
        }        
    }

    return (
        userSession &&
        <div className='Wrapper-flex'>
            <table className="Table">
                <caption><h2>Clientes</h2></caption>
                <thead>
                    <tr>
                        <td colSpan={3}><input type="search" onChange={searchChange} onKeyPress={searchKeyPress} placeholder='Buscar...' autoFocus /></td>
                        <td colSpan={2}>
                            <Link to='/customer-insert'>
                                <BtnAccept text='Nuevo' />
                            </Link>
                        </td>
                    </tr>
                    <tr>
                        <th>Correo</th>
                        <th>Nombre</th>
                        <th>Telfono</th>
                        <th>Activo</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        customers.map(customer =>
                            <tr key={customer.id} id={customer.id} onClick={rowClick}>
                                <td>{customer.email}</td>
                                <td>{customer.fullname}</td>
                                <td>{customer.phone}</td>
                                <td className="Table-cell-center">{customer.isActive && <AiFillCheckCircle className="Table-icon" />}</td>
                                <td>
                                    { // only if is admin
                                        userSession.isAdmin &&
                                        <BtnDanger text='Eliminar' onClick={() => btnDeleteClick(customer.id)} />
                                    }
                                </td>
                            </tr>
                        )
                    }
                </tbody>
                <tfoot>
                    <tr>
                        {<td className='Table-cell-center' colSpan={4}>{customers.length} {customers.length === 1 ? 'cliente' : 'clientes'}</td>}
                    </tr>
                </tfoot>
            </table>

            
            <div className='Card'>
                <div className='Card-customer-border' >

                    { // show only if userSelected not is null
                        customerSelected != null &&
                        <div className='Card-customer-container' >
                            <div className='Card-customer-data'>
                                <h5>Correo</h5>
                                <pre>{customerSelected['email']}</pre>
                                <h5>Nombre</h5>
                                <pre>{customerSelected['fullname']}</pre>
                                <h5>Telfono</h5>
                                <pre>{customerSelected['phone']}</pre>
                                <h5>Calle y nmero</h5>
                                <pre>{customerSelected['streetAndNumber']}</pre>
                                <h5>Colonia</h5>
                                <pre>{customerSelected['suburb']}</pre>
                                <h5>CP</h5>
                                <pre>{customerSelected['cp']}</pre>
                                <h5>Ciudad</h5>
                                <pre>{customerSelected['town']}</pre>
                            </div>
                            { // only if is admin
                                userSession.isAdmin &&
                                <BtnPrimary
                                    text='Modificar'
                                    style={{ display: 'block' }}
                                    onClick={() => navigate(`/customer-update/${customerSelected['id']}`)}
                                />
                            }
                        </div>
                    }
                </div>
            </div>
            <ModalDelete
                isShowModal={stateModalDelete['isShowModal']}
                subject={'Ests segur@ de eliminar al cliente?'}
                btnDeleteClick={onDelete}
                btnCancelClick={btnCancelClick}
            />
        </div>
    )
}

export default Customers