import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router'
// context
import { DataContext } from '../../context/DataContext'
// components
import FormMessage from '../form/FormMessage'

const CustomerPassLost = () => {
    // context
    const { customerSession, fetchGet, fetchPut } = useContext(DataContext)
    // states
    const [email, setEmail] = useState('')
    const [formMessage, setFormMessage] = useState({
        msg: '',
        isSuccess: false
    })
    // navigate
    const navigate = useNavigate()

    useEffect(() => {
        if (customerSession) navigate('/not-found')
    }, [customerSession, navigate, fetchGet, fetchPut])

    async function onSubmit(e) {
        e.preventDefault()

        // get idCustomer from email
        let res = await fetchGet(`/index.php?fn=getIdCustomerFromEmail&email=${email}`)
        //console.log(res) // {idCustomer: <int>}
        if (res !== null) {
            if (res['idCustomer'] > -1) {
                // send email for recovery pass
                res = await fetchGet(`/index.php?fn=sendEmailRecoveryPass&id=${res['idCustomer']}&email=${email}`)
                //console.log(res) // isSendedEmailRecoveryPass: bool
                if (res !== null && res['isSendedEmailRecoveryPass']) {
                    setFormMessage({
                        msg: 'Correo enviado. Ingresa a tu correo\npara restaurar tu contrasea.',
                        isSuccess: true
                    })
                }
            }
            else {
                setFormMessage({
                    msg: 'El correo no se encuentra registrado',
                    isSuccess: false
                })
            }
        }
    }

    function inputChange(e) {
        setEmail(e.target.value)
    }

    function btnCloseClick() {
        setFormMessage({
            ...formMessage, // same values
            msg: '' // only change this value
        })
    }

    return (
        !customerSession &&
        <>
            <p className='ProductsTitle'>OLVID MI CONTRASEA</p>
            <div className='Wrapper-flex'>
                <div className='FormCard'>
                    {!formMessage.isSuccess &&
                        <form onSubmit={onSubmit}>
                            <input
                                type='email'
                                name='email'
                                placeholder='Correo'
                                value={email}
                                onChange={inputChange}
                            />
                            <input type='submit' value='Enviarme correo de restauracin' />
                        </form>
                    }
                    {
                        formMessage.msg.length > 0 &&
                        <FormMessage msg={formMessage.msg} btnCloseClick={btnCloseClick} isSuccess={formMessage.isSuccess} />
                    }
                </div>
            </div>
        </>
    )
}

export default CustomerPassLost