import React, { useContext, useEffect } from 'react'

// components
import FormCustomerPass from '../form/FormCustomerPass'

// context
import { DataContext } from '../../context/DataContext'
import { useNavigate } from 'react-router'

const CustomerPass = () => {
  const { customerSession } = useContext(DataContext) 

  const navigate = useNavigate()

  useEffect(() => {
    if (customerSession === null) navigate('/not-found')    
    return () => {}
  }, [customerSession, navigate])
  

  return (
    customerSession &&
    <>
      <p className='ProductsTitle'>CAMBIAR CONTRASEA</p>
      <div className='Wrapper-flex'>
        <FormCustomerPass customerId={customerSession ? customerSession.id : -1} />
      </div>
    </>
  )
}

export default CustomerPass