import { useContext, useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';

// context
import { DataContext } from '../../context/DataContext'

// css
import './Products.css';

export default function Products() {
    // context
    const { fetchGet } = useContext(DataContext)

    // params
    const params = useParams(); // important!
    //console.log(params); // {search: 'categoria'|'texto', txtSearch: <string>}

    const navigate = useNavigate();

    // states
    const [articles, setArticles] = useState([]);
    
    useEffect(() => {
        //console.log('>>> effect Productos')

        async function articlesLoad() {
            let res
            if (params['search'] === 'categoria')  
                res = await fetchGet('/index.php?fn=getAllArticlesFromCategoryName&categoryName=' + params['txtSearch'])
            else 
                res = await fetchGet('/index.php?fn=getAllArticlesFromTxt&txtSearch=' + params['txtSearch'])
            //console.log(res);
            
            if (res !== null) setArticles(res['articles']);                    
        }  

        articlesLoad();

        return () => {
            //console.log('useEffect Productos BYE')
        }

    }, [params, fetchGet]);

    const imgClick = (id) => {
        //console.log('click to id: ' + id);
        navigate('/producto-detalle/' + id);
    } 

    return (
        <>            
            <p className='ProductsTitle'>{params['txtSearch']}</p>
            <div className='Grid'>
                {
                    articles.map(article =>
                        article['isAvailable'] &&
                        <div className='Product-card' key={article.id}>
                            {
                                // if without else
                                article.stock === 0 && <span>AGOTADO</span>
                            }
                            <img src={article.imgPath} alt={article.title} onClick={() => imgClick(article.id)} width="220" height="250" />
                            <p>{article.articleName}</p>
                            <p className='Product-price'>$ {article.price}</p>
                        </div>
                    )
                }
            </div>
        </>
    );
}