import { useState, useContext, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
// components
import FormMessage from '../form/FormMessage';
// context
import { DataContext } from '../../context/DataContext'

export default function CustomerInput() {
    // context
    const { fetchPostInput, customerSession, setCustomerSession, isReturnToCart, setIsReturnToCart } = useContext(DataContext)
    // states
    const [txtInputs, setTxtInputs] = useState({
        email: '',
        pass: ''
    })    
    const [msg, setMsg] = useState('')

    // navigate
    const navigate = useNavigate()

    useEffect(() => {
        //console.log('useEffect-CustomerInput')

        if (customerSession) navigate('/')

        return () => { 
            //console.log('useEffect-CustomerInput BYE') 
        }
    }, [customerSession, navigate])

    const txtChange = (e) => {
        //console.log(e.target)
        setTxtInputs({
            ...txtInputs,
            [e.target.name]: e.target.value
        })
    }

    const onSubmit = async (e) => {
        e.preventDefault()
        let res = await fetchPostInput('/customer_authorized.php?fn=getDataCustomerFromPass', txtInputs)
        setTxtInputs({
            email: '',
            pass: ''
        })
        //('res onSubmit:', res)

        // email and pass OK?
        if (res.isAuth) {
            res = res.customer
            //console.log(res);
            if (res.isActive) {
                // save data on local-storage
                window.sessionStorage.clear()
                window.sessionStorage.setItem('customer', JSON.stringify(res))
                setCustomerSession(res);
                // verify if comming from cart
                if (isReturnToCart) {
                    //console.log('isReturnToCart')
                    setIsReturnToCart(false)
                    navigate('/carrito')
                }
                else navigate('/')
            }
            else setMsg('Tu cuenta no est activada')
        }
        else setMsg('Correo y/o contrasea incorrecta')
        setTxtInputs({
            ...txtInputs, // same values
            pass: '' // only change 1 property
        })
    }

    const onCloseFormMessage = () => {
        setMsg('');
    }

    return (
        !customerSession &&
        <div className='Wrapper-flex'>
            <div className="FormCard">
                <h3>INICIA SESIN</h3>
                <form onSubmit={onSubmit}>
                    <input
                        type="email"
                        name="email"
                        value={txtInputs.email}
                        onChange={txtChange}
                        placeholder='Correo'
                        required
                    />
                    <input
                        type="password"
                        name="pass"
                        value={txtInputs.pass}
                        onChange={txtChange}
                        placeholder='Contrasea'
                        required
                    />
                    <Link to="/customer-pass-lost">
                        <span>Olvid mi contrasea</span>
                    </Link>
                    <input type="submit" value="INICIAR SESIN" />
                </form>
                <p>No tienes una cuenta? <Link to="/customer-register">Crear una cuenta</Link></p>
                {
                    msg !== '' &&
                    <FormMessage msg={msg} btnCloseClick={onCloseFormMessage} />
                }
            </div>
        </div>
    )
}