import React, { useContext, useState, useEffect, useCallback } from 'react'
// components
import FormMessage from '../form/FormMessage'
// context
import { DataContext } from '../../context/DataContext'

const FormCustomerData = ({ isAutoRegister }) => {
    // context
    const { fetchGet, fetchPost, fetchPut, customerSession } = useContext(DataContext)

    const inputsDefaultValues = {
        email: '',
        fullname: '',
        phone: '',
        streetAndNumber: '',
        suburb: '',
        cp: '',
        town: '',
        pass: '',
        confirm: ''
    }

    // states
    const [inputs, setInputs] = useState(inputsDefaultValues)
    const [msg, setMsg] = useState('')
    const [isSuccess, setIsSuccess] = useState(false)

    const loadCustomerData = useCallback(async () => {
        let res = await fetchGet(
            `/customer_authorized.php?fn=getCustomer&id=${customerSession['id']}`,
            customerSession['token']
        )
        if (res !== null) {
            res = res['customer']
            setInputs({
                email: res['email'],
                fullname: res['fullname'],
                phone: res['phone'],
                streetAndNumber: res['streetAndNumber'],
                suburb: res['suburb'],
                cp: res['cp'],
                town: res['town'],
                pass: '',
                confirm: ''
            })
        }
    }, [fetchGet, customerSession])

    useEffect(() => {
        if (customerSession) loadCustomerData() // use-callback for prevent execute infinite

        return () => { }
    }, [loadCustomerData, customerSession])


    function inputChange(e) {
        setInputs({
            ...inputs, // same values
            [e.target.name]: e.target.value // only change property same name
        })
    }

    async function formSubmit(e) {
        e.preventDefault()
        let res
        if (isAutoRegister) {
            res = await fetchGet(`/index.php?fn=isExistsCustomer&email=${inputs.email}&id=-1`)
            //console.log(res)
            if (res !== null) {
                if (!res['isExistsCustomer']) {
                    // verify pass and confirm
                    if (inputs.pass === inputs.confirm) {

                        // save new customer
                        res = await fetchPost(
                            `/index.php?fn=insertCustomer&isAutoregister=${isAutoRegister}`,
                            inputs
                        )
                        //console.log(res)
                        if (res !== null) {
                            setIsSuccess(true)
                            setMsg('El cliente ha sido registrado, para activar tu cuenta ingresa a tu correo')
                        }
                    }
                    else {
                        setInputs({
                            ...inputs,
                            pass: '', confirm: ''
                        })
                        setMsg('Contrasea y confirmacin son diferentes')
                    }

                }
                else setMsg('El correo ya se encuentra registrado')
            }
        }
        else {
            res = await fetchPut('/customer_authorized.php?fn=updateCustomer&id=' + customerSession['id'],
                inputs,
                customerSession['token']
            )
            if (res !== null) {
                setIsSuccess(true)
                setMsg('Los datos han sido actualizados')
            }
        }
    }

    return (
        <div className='Card'>
            <form className='Form' onSubmit={formSubmit} style={{ display: isSuccess ? 'none' : 'block' }}>
                <label htmlFor='email'>Correo</label>
                <input
                    type='email'
                    name='email'
                    id='email'
                    value={inputs.email}
                    onChange={inputChange}
                    readOnly={!isAutoRegister}
                    autoFocus={isAutoRegister}
                    required={true}
                    style={{
                        backgroundColor: isAutoRegister ? 'white' : 'gainsboro'
                    }}
                />
                <label htmlFor='fullname'>Nombre completo</label>
                <input
                    type='text'
                    name='fullname'
                    id='fullname'
                    value={inputs.fullname}
                    onChange={inputChange}
                    autoFocus={!isAutoRegister}
                    required
                />
                <label htmlFor='phone'>Telfono</label>
                <input
                    type='text'
                    name='phone'
                    id='phone'
                    value={inputs.phone}
                    onChange={inputChange}
                    required
                />
                <label htmlFor='streetAndNumber'>Calle y nmero</label>
                <input
                    type='text'
                    name='streetAndNumber'
                    id='streetAndNumber'
                    value={inputs.streetAndNumber}
                    onChange={inputChange}
                    required
                />
                <div className='Form-input-pair-inline'>
                    <div>
                        <label htmlFor='suburb'>Colonia</label>
                        <input
                            type='text'
                            name='suburb'
                            id='suburb'
                            value={inputs.suburb}
                            onChange={inputChange}
                            required
                        />
                    </div>
                    <div>
                        <label htmlFor='cp'>C.P.</label>
                        <input
                            type='text'
                            name='cp'
                            id='cp'
                            value={inputs.cp}
                            onChange={inputChange}
                            required
                        />
                    </div>
                </div>
                <label htmlFor='town'>Municipio</label>
                <input
                    type='text'
                    name='town'
                    id='town'
                    value={inputs.town}
                    onChange={inputChange}
                    required
                />
                <div className='Form-input-pair-inline' style={{display: isAutoRegister ? 'flex' : 'none'}}>
                    <div style={{ width: '50%' }}>
                        <label htmlFor='pass'>Contrasea</label>
                        <input
                            type='password'
                            name='pass'
                            id='pass'
                            value={inputs.pass}
                            onChange={inputChange}
                            required={isAutoRegister}
                        />
                    </div>
                    <div style={{ width: '50%' }}>
                        <label htmlFor='confirm'>Confirmar</label>
                        <input
                            type='password'
                            name='confirm'
                            id='confirm'
                            value={inputs.confirm}
                            onChange={inputChange}
                            required={isAutoRegister}
                        />
                    </div>
                </div>
                {
                    !isSuccess &&
                    <input
                        type="submit"
                        value="Guardar"
                        style={{
                            backgroundColor: 'black',
                            color: 'white',
                            height: '40px',
                            marginTop: '20px',
                            fontSize: '1rem',
                            cursor: 'pointer'
                        }}
                    />
                }
            </form>
            {
                msg.length !== 0 &&
                <FormMessage msg={msg} btnCloseClick={() => setMsg('')} isSuccess={isSuccess} />
            }
        </div>
    )
}

export default FormCustomerData